import { FieldArray, FormikErrors, FormikProps, FormikTouched } from 'formik';
import { styles } from '../styles';
import { FlatList, Image, View } from 'react-native';
import { BottomSelectorOption, Button, Text } from 'tradewize-component';
import { icon_minus } from '../../../assets/images';
import { initialValues, KEY_COMPANY_INFORMATION, LIST_ITEMS } from '../model';
import Item from './Item';
import Footer from './Footer';
import { useTranslation } from 'react-i18next';
import { FieldItem } from '../../EnterYourInformation/model';

interface CompanyInformationProps {
  item: FieldItem;
  values: (typeof initialValues)[typeof KEY_COMPANY_INFORMATION] | unknown;
  errors: any;
  touched: any;
  setTouched: (touched: Record<string, boolean>) => void;
  setSelectedOption: (options: BottomSelectorOption[]) => void;
  setSelectedField: (field: string) => void;
  selectedOptionValue: any;
  setIsSelectBoxVisible: (visible: boolean) => void;
  formRef: React.RefObject<FormikProps<typeof initialValues>> | null;
  formRefCompanyInformation: any;
}

const CompanyInformation = (props: CompanyInformationProps) => {
  const { t } = useTranslation();
  const {
    values,
    errors,
    touched,
    setTouched,
    setSelectedOption,
    setSelectedField,
    selectedOptionValue,
    setIsSelectBoxVisible,
    formRef,
    formRefCompanyInformation,
  } = props;

  return (
    <FieldArray name={KEY_COMPANY_INFORMATION}>
      {({ push: pushCompanyInformation, remove }) => {
        formRefCompanyInformation.current = pushCompanyInformation;

        return (
          <View style={styles.addCompanyInformationContainer}>
            {(
              values as unknown as (typeof initialValues)[typeof KEY_COMPANY_INFORMATION]
            )?.map((company, index) => {
              return (
                <View
                  style={styles.containerCompanyInformation}
                  key={index.toString()}
                >
                  <View style={styles.headerCompanyInformation}>
                    <Text style={styles.title}>
                      {t('LIST_YOUR_EXPERIENCE_COMPANY_NAME')}
                    </Text>

                    {(
                      values as unknown as (typeof initialValues)[typeof KEY_COMPANY_INFORMATION]
                    )?.length > 1 && (
                      <Button
                        variant="ghost"
                        title={t('DELETE_COMPANY')}
                        onPress={() => remove(index)}
                      >
                        <Image source={icon_minus} style={styles.iconMinus} />
                      </Button>
                    )}
                  </View>
                  <FlatList
                    nestedScrollEnabled={true}
                    scrollEnabled={false}
                    data={LIST_ITEMS}
                    renderItem={({ item }) => (
                      <View style={styles.listCompanyInformationItemContainer}>
                        <Item
                          index={index}
                          item={item}
                          values={company}
                          handleChange={(field: string) => (value: string) => {
                            formRef?.current?.setFieldValue(
                              `${KEY_COMPANY_INFORMATION}[${index}].${field}`,
                              value,
                            );
                          }}
                          errors={errors?.[index]}
                          touched={touched?.[index]}
                          setTouched={setTouched}
                          setSelectedOption={setSelectedOption}
                          setSelectedField={setSelectedField}
                          selectedOptionValue={selectedOptionValue}
                          setIsSelectBoxVisible={setIsSelectBoxVisible}
                          formRef={
                            formRef as React.RefObject<
                              FormikProps<typeof initialValues>
                            >
                          }
                          fieldBasePath={`${KEY_COMPANY_INFORMATION}[${index}]`}
                        />
                      </View>
                    )}
                    keyExtractor={(_item, i) => i.toString()}
                    ListFooterComponent={
                      <Footer
                        item={undefined as unknown as FieldItem}
                        values={
                          values as unknown as (typeof initialValues)[typeof KEY_COMPANY_INFORMATION][number]
                        }
                        errors={
                          errors as FormikErrors<
                            (typeof initialValues)[typeof KEY_COMPANY_INFORMATION]
                          >
                        }
                        touched={
                          touched as FormikTouched<
                            (typeof initialValues)[typeof KEY_COMPANY_INFORMATION]
                          >
                        }
                        setTouched={setTouched}
                        setSelectedOption={setSelectedOption}
                        setSelectedField={setSelectedField}
                        selectedOptionValue={selectedOptionValue}
                        setIsSelectBoxVisible={setIsSelectBoxVisible}
                        formRef={
                          formRef as React.RefObject<
                            FormikProps<typeof initialValues>
                          >
                        }
                        companyIndex={index}
                      />
                    }
                  />
                </View>
              );
            })}
          </View>
        );
      }}
    </FieldArray>
  );
};

export default CompanyInformation;
