import { View, Text, FlatList, ScrollView, Image } from 'react-native';
import { styles } from './styles';
import { useTranslation } from 'react-i18next';
import { useDispatch, useSelector } from 'react-redux';
import { useEffect, useMemo, useState, useCallback, useRef } from 'react';
import {
  getCompanyDropdownAsync,
  getOnboardingQuestionAsync,
  toggleCompanyCheckbox,
} from '../../redux/features/register/actions';
import { RootState } from '../../redux/store/rootReducer';
import { Button, CheckboxButton } from 'tradewize-component';
import colors from '../../theme/colors';
import { StateData } from '../../redux/features/register/types';
import Question from './components/Question';
import {
  CompanyAnswer,
  handleDeleteImage,
  handleSubmit,
  handleUploadMedia,
} from '../../helper/register';
import { IconClose } from '../../assets/svg';
import { SvgXml } from 'react-native-svg';

const UnderstandingYourKnowledge = () => {
  const scrollViewRef = useRef<ScrollView>(null);
  const itemRefs = useRef<{ [key: string]: View | null }>({});
  const dispatch = useDispatch();
  const { t } = useTranslation();

  const { getCompanyDropdown, getAllOnboardingQuestion } = useSelector(
    (state: RootState) => state.register,
  );
  const errorQuestion = useSelector(
    (state: RootState) => state.register.errorQuestion,
  );

  const [companyAnswers, setCompanyAnswers] = useState<CompanyAnswer[]>([]);
  const [images, setImages] = useState<any[]>([]);

  useEffect(() => {
    dispatch(getCompanyDropdownAsync());
  }, [dispatch]);

  // Function ƒë·ªÉ cu·ªôn ƒë·∫øn item ch·ªâ ƒë·ªãnh
  const scrollToItem = useCallback((itemKey: string) => {
    const itemRef = itemRefs.current[itemKey];
    if (itemRef && scrollViewRef.current) {
      itemRef.measureLayout(
        scrollViewRef.current as any,
        (x, y, _width, _height) => {
          scrollViewRef.current?.scrollTo({
            y: y - 50,
            animated: true,
          });
        },
        () => {
          console.log('Error measuring layout');
        },
      );
    }
  }, []);

  useEffect(() => {
    if (errorQuestion?.length > 0) {
      scrollToItem(errorQuestion[0]);
    }
  }, [errorQuestion, scrollToItem]);

  const handleAnswerChange = useCallback(
    (companyKey: string, answer: 'yes' | 'no') => {
      setCompanyAnswers(prevAnswers => {
        const existingAnswer = prevAnswers.find(
          item => item.companyKey === companyKey,
        );

        if (existingAnswer) {
          // If clicking the same answer, toggle it off
          if (existingAnswer.answer === answer) {
            return prevAnswers.filter(item => item.companyKey !== companyKey);
          }
          // Otherwise, update to the new answer
          return prevAnswers.map(item =>
            item.companyKey === companyKey ? { ...item, answer } : item,
          );
        }

        // Add new answer
        return [...prevAnswers, { companyKey, answer }];
      });
    },
    [],
  );

  const renderNecessaryTools = useMemo(() => {
    // Th√™m error handling
    if (!getCompanyDropdown?.data || !Array.isArray(getCompanyDropdown?.data)) {
      return null;
    }

    const companyDropdownData = getCompanyDropdown?.data;
    const questionSelected = companyDropdownData?.filter(
      (item: StateData) => item?.isChecked,
    );

    const getCompanyAnswer = (companyKey: string): 'yes' | 'no' | null => {
      const answer = companyAnswers.find(
        item => item.companyKey === companyKey,
      );
      // Tr·∫£ v·ªÅ 'no' m·∫∑c ƒë·ªãnh n·∫øu ch∆∞a c√≥ answer
      return answer?.answer ?? 'no';
    };

    return (
      <View>
        {questionSelected &&
          questionSelected.length > 0 &&
          questionSelected.map((element: StateData) => {
            const currentAnswer = getCompanyAnswer(element.key);
            const isYes = currentAnswer === 'yes';
            const isNo = currentAnswer === 'no';

            return (
              <View
                ref={ref => {
                  itemRefs.current[element.key] = ref;
                }}
                style={[
                  styles.boxNecessaryTools,
                  errorQuestion?.includes(element?.key) &&
                    styles.boxNecessaryToolsError,
                ]}
                key={element?.key}
              >
                <Text style={styles.titleNecessaryTools}>
                  {t('DO_YOU_NEED_TOOLS_FOR_YOUR_JOB', {
                    companyName: element?.value,
                  })}
                </Text>
                <View style={styles.groundCheckboxButton}>
                  <CheckboxButton
                    label={t('YES')}
                    textStyle={styles.checkboxButtonText}
                    onChange={() => handleAnswerChange(element.key, 'yes')}
                    isChecked={isYes}
                    checkboxStyle={[
                      styles.checkboxButtonStyle,
                      isYes && {
                        backgroundColor: colors.white,
                        borderColor: colors.mainColorPrimary,
                      },
                    ]}
                    customCheckmark={
                      isYes ? <View style={styles.checkmarkBox} /> : null
                    }
                  />
                  <CheckboxButton
                    label={t('NO')}
                    textStyle={styles.checkboxButtonText}
                    onChange={() => handleAnswerChange(element.key, 'no')}
                    isChecked={isNo}
                    checkboxStyle={[
                      styles.checkboxButtonStyle,
                      isNo && {
                        backgroundColor: colors.white,
                        borderColor: colors.mainColorPrimary,
                      },
                    ]}
                    customCheckmark={
                      isNo ? <View style={styles.checkmarkBox} /> : null
                    }
                  />
                </View>

                {isYes && (
                  <View style={styles.groundButtonMedia}>
                    <Button
                      style={styles.buttonMedia}
                      title={t('UPLOAD_IMAGE')}
                      onPress={() => {
                        handleUploadMedia({
                          companyKey: element.key,
                          type: 'picker',
                          images,
                          setImages,
                        });
                      }}
                    />
                    <Button
                      style={styles.buttonMedia}
                      title={t('TAKE_PHOTO')}
                      onPress={() => {
                        handleUploadMedia({
                          companyKey: element.key,
                          type: 'takePhoto',
                          images,
                          setImages,
                        });
                      }}
                    />
                  </View>
                )}
                <View style={styles.groundImages}>
                  {images?.length > 0 &&
                    images
                      ?.find(item => item.companyKey === element.key)
                      ?.images?.map((item: any) => {
                        return (
                          <View key={item.id}>
                            <Image
                              source={{ uri: item.uri }}
                              style={styles.image}
                            />

                            <Button
                              activeOpacity={1}
                              size="small"
                              variant="ghost"
                              style={styles.imageDeleteButton}
                              onPress={() =>
                                handleDeleteImage({
                                  companyKey: element.key,
                                  imageId: item.id,
                                  setImages,
                                })
                              }
                            >
                              <SvgXml
                                xml={IconClose(colors.white)}
                                width={18}
                                height={18}
                              />
                            </Button>
                          </View>
                        );
                      })}
                </View>
              </View>
            );
          })}
      </View>
    );
  }, [
    getCompanyDropdown,
    t,
    companyAnswers,
    handleAnswerChange,
    images,
    errorQuestion,
  ]);

  return (
    <>
      <ScrollView
        ref={scrollViewRef}
        nestedScrollEnabled
        style={styles.container}
      >
        <View style={styles.boxCompany}>
          <Text style={styles.titleCompany}>{t('SELECT_JOB_DESCRIPTION')}</Text>
          <View style={styles.line} />

          <FlatList
            scrollEnabled={false}
            nestedScrollEnabled
            numColumns={4}
            columnWrapperStyle={styles.contentContainerCheckboxButton}
            data={getCompanyDropdown?.data || []}
            renderItem={({ item }) => {
              return (
                <CheckboxButton
                  style={styles.checkboxButton}
                  label={item?.value}
                  textStyle={styles.checkboxButtonText}
                  onChange={() => {
                    dispatch(toggleCompanyCheckbox(item.key));

                    // Check if onboarding question data already exists for this company
                    const existingData = getAllOnboardingQuestion?.data;

                    const hasDataForCompany =
                      Array.isArray(existingData) &&
                      existingData.some(
                        (question: any) => question.id === item.key,
                      );

                    // Only call API if data doesn't exist for this company
                    if (!hasDataForCompany) {
                      dispatch(getOnboardingQuestionAsync(item.key));
                    }
                  }}
                  isChecked={item?.isChecked}
                  checkboxStyle={
                    item?.isChecked
                      ? {
                          backgroundColor: colors.mainColorPrimary,
                          borderColor: colors.mainColorPrimary,
                        }
                      : undefined
                  }
                />
              );
            }}
            keyExtractor={(item, index) => `${item?.key}_${index.toString()}`}
          />
        </View>

        {renderNecessaryTools}
        <Question itemRefs={itemRefs} />
      </ScrollView>
      <Button
        variant="primary"
        size="large"
        style={styles.btnSubmit}
        title={t('SUBMIT')}
        onPress={() => {
          handleSubmit(images, companyAnswers);
        }}
      />
    </>
  );
};

export default UnderstandingYourKnowledge;
