import { FieldArray, FormikProps } from 'formik';
import { FieldItem } from '../../EnterYourInformation/model';
import {
  initialValues,
  ItemProps,
  KEY_COMPANY_INFORMATION,
  LIST_ITEMS_PROJECTS,
} from '../model';
import { FlatList, Image, View } from 'react-native';
import { styles } from '../styles';
import { Button, Text } from 'tradewize-component';
import { useTranslation } from 'react-i18next';
import { icon_minus } from '../../../assets/images';
import Item from './Item';
import { generateUUID } from '../../../utils';

interface FooterProps extends ItemProps {
  item: FieldItem;
  values:
    | (typeof initialValues)[typeof KEY_COMPANY_INFORMATION][number]
    | unknown;
  errors: any;
  touched: any;
  setTouched: (touched: Record<string, boolean>) => void;
  companyIndex: number;
}

const Footer = (props: FooterProps) => {
  const { t } = useTranslation();

  const {
    values,
    companyIndex,
    errors,
    touched,
    setTouched,
    setSelectedOption,
    setSelectedField,
    selectedOptionValue,
    setIsSelectBoxVisible,
    formRef,
  } = props;

  const company = (
    values as unknown as (typeof initialValues)[typeof KEY_COMPANY_INFORMATION]
  )[companyIndex];
  const projects = company?.PROJECTS ?? [];
  const arrFinal = projects.flatMap(() =>
    LIST_ITEMS_PROJECTS.map((item: FieldItem) => ({ ...item })),
  );

  return (
    <FieldArray name={`${KEY_COMPANY_INFORMATION}[${companyIndex}].PROJECTS`}>
      {({ push, remove }) => {
        return (
          <View>
            {arrFinal?.length > 0 && (
              <View style={styles.listCompanyProjectItemContainer}>
                <FlatList
                  nestedScrollEnabled={true}
                  scrollEnabled={false}
                  data={arrFinal || []}
                  renderItem={({ item, index }) => {
                    const projectIndex = Math.floor(
                      index / LIST_ITEMS_PROJECTS.length,
                    );
                    return (
                      <View
                        style={[
                          styles.itemCompanyProjectContainer,
                          index % 2 === 0 && styles.itemEven,
                          index % 2 !== 0 && styles.itemOdd,
                        ]}
                      >
                        {index % 2 === 0 && (
                          <View style={styles.itemEvenContainer}>
                            <Text style={styles.labelItemEven}>
                              {t('COMPANY_PROJECT_NUMBER', {
                                number: projectIndex + 1,
                              })}
                            </Text>
                            <Button
                              size="small"
                              variant="ghost"
                              onPress={() => remove(projectIndex)}
                            >
                              <Image
                                source={icon_minus}
                                style={styles.iconMinus}
                              />
                            </Button>
                          </View>
                        )}

                        <Item
                          // index={Math.floor(index / LIST_ITEMS_PROJECTS.length)}
                          item={item}
                          values={projects[projectIndex]}
                          handleChange={(field: string) => (value: string) => {
                            formRef?.current?.setFieldValue(
                              `${KEY_COMPANY_INFORMATION}[${companyIndex}].PROJECTS[${projectIndex}].${field}`,
                              value,
                            );
                          }}
                          errors={
                            errors?.[companyIndex]?.PROJECTS?.[projectIndex]
                          }
                          touched={
                            touched?.[companyIndex]?.PROJECTS?.[projectIndex]
                          }
                          setTouched={setTouched}
                          setSelectedOption={setSelectedOption}
                          setSelectedField={setSelectedField}
                          selectedOptionValue={selectedOptionValue}
                          setIsSelectBoxVisible={setIsSelectBoxVisible}
                          formRef={
                            formRef as React.RefObject<
                              FormikProps<typeof initialValues>
                            >
                          }
                          fieldBasePath={`${KEY_COMPANY_INFORMATION}[${companyIndex}].PROJECTS[${projectIndex}]`}
                        />
                      </View>
                    );
                  }}
                  keyExtractor={(_item, index) => index.toString()}
                />
              </View>
            )}
            <Button
              size="small"
              style={styles.buttonAddCompanyProject}
              title={t('ADD_COMPANY_PROJECT')}
              onPress={() =>
                push({
                  ID: generateUUID(),
                  COMPANY_PROJECT_NAME: '',
                  COMPANY_PROJECT_DESCRIPTION: '',
                })
              }
            />
            <View style={styles.spaceBetweenContainer} />
          </View>
        );
      }}
    </FieldArray>
  );
};

export default Footer;
