import { StyleSheet } from 'react-native';
import { Button, Text, WheelMonthYearPicker } from 'tradewize-component';
import colors from '../../../theme/colors';
import { IconCalendar } from '../../../assets/svg';
import { SvgXml } from 'react-native-svg';
import { useTranslation } from 'react-i18next';
import { useState } from 'react';
import { logger } from '../../../utils';
import dayjs from 'dayjs';

interface InputDateProps {
  onChange: (date: Date) => void;
  hasError?: boolean;
  onOpen?: () => void;
}

const InputDate = (props: InputDateProps) => {
  const { onChange, hasError = false, onOpen } = props;

  const { t } = useTranslation();
  const [isVisible, setIsVisible] = useState(false);
  const [selectedDate, setSelectedDate] = useState<Date | null>(null);

  const onPress = () => {
    try {
      setIsVisible(true);
      onOpen?.();
    } catch (error) {
      logger('error', error);
    }
  };

  return (
    <>
      <Button
        style={[styles.button, hasError && styles.buttonError]}
        variant="secondary"
        onPress={onPress}
      >
        <Text
          numberOfLines={1}
          style={[
            styles.txtDate,
            { color: selectedDate ? colors.black : colors.grayScale70 },
          ]}
        >
          {selectedDate
            ? dayjs(selectedDate).format('MM-YYYY')
            : t('SELECT_A_DATE')}
        </Text>
        <SvgXml xml={IconCalendar(colors.grayScale70)} width={24} height={24} />
      </Button>

      <WheelMonthYearPicker
        isVisible={isVisible}
        initialDate={selectedDate || new Date()}
        maxDate={new Date()}
        styleButtonCancel={styles.cancelButton}
        styleButtonConfirm={styles.confirmButton}
        styleTextCancel={styles.cancelButtonText}
        styleTextConfirm={styles.confirmButtonText}
        textCancel={t('CANCEL')}
        textConfirm={t('CONFIRM')}
        handleSelectedDate={(date: Date) => {
          onChange(date);
          setSelectedDate(date);
          setIsVisible(false);
        }}
        onCancel={() => {
          setIsVisible(false);
        }}
      />
    </>
  );
};

const styles = StyleSheet.create({
  button: {
    backgroundColor: colors.additionalSecondary,
    justifyContent: 'space-between',
  },
  buttonText: {
    color: colors.black,
    fontSize: 14,
    fontWeight: '400',
    lineHeight: 20,
    textAlign: 'left',
    flex: 1,
    paddingRight: 8,
  },
  cancelButton: {
    backgroundColor: colors.white,
    borderWidth: 1,
    borderColor: colors.line,
  },
  cancelButtonText: {
    color: colors.black,
    fontWeight: 'bold',
    fontSize: 16,
    lineHeight: 24,
  },
  confirmButtonText: {
    color: colors.white,
    fontWeight: 'bold',
    fontSize: 16,
    lineHeight: 24,
  },
  confirmButton: {
    backgroundColor: colors.mainColorPrimary,
  },
  txtDate: {
    fontSize: 14,
    fontWeight: '400',
    lineHeight: 20,
    textAlign: 'left',
  },
  buttonError: {
    borderColor: colors.error,
    borderWidth: 1,
  },
});

export default InputDate;
