import { useState, useEffect, useRef, useCallback, useMemo } from 'react';
import BackgroundTimer from 'react-native-background-timer';

export function useCountdown(duration: number, onFinish?: () => void) {
  const [remaining, setRemaining] = useState(() => duration);
  const [isRunning, setIsRunning] = useState(false);

  const intervalRef = useRef<number | null>(null);
  const hasFinishedRef = useRef(false); // ‚úÖ ƒë·∫£m b·∫£o onFinish ch·ªâ g·ªçi 1 l·∫ßn

  const clear = useCallback(() => {
    if (intervalRef.current !== null) {
      BackgroundTimer.clearInterval(intervalRef.current);
      intervalRef.current = null;
    }
  }, []);

  const tick = useCallback(() => {
    setRemaining(prev => {
      const next = Math.max(prev - 1000, 0);
      if (next === 0) {
        clear();
        setIsRunning(false);
        if (!hasFinishedRef.current) {
          hasFinishedRef.current = true;
          onFinish?.(); // ‚úÖ ch·ªâ g·ªçi ƒë√∫ng 1 l·∫ßn
        }
      }
      return next;
    });
  }, [clear, onFinish]);

  const start = useCallback(() => {
    if (isRunning) return;
    if (remaining <= 0) return; // h·∫øt gi·ªù r·ªìi th√¨ kh√¥ng ch·∫°y l·∫°i
    hasFinishedRef.current = false;
    setIsRunning(true);
    intervalRef.current = BackgroundTimer.setInterval(tick, 1000);
  }, [isRunning, tick, remaining]);

  const stop = useCallback(() => {
    if (!isRunning) return;
    clear();
    setIsRunning(false);
  }, [isRunning, clear]);

  const reset = useCallback(() => {
    clear();
    setRemaining(duration);
    setIsRunning(false);
    hasFinishedRef.current = false;
  }, [duration, clear]);

  useEffect(() => {
    setRemaining(duration);
    hasFinishedRef.current = false;
    return () => clear();
  }, [duration, clear]);

  const formatted = useMemo(() => {
    const totalSeconds = Math.floor(remaining / 1000);
    const days = Math.floor(totalSeconds / 86400);
    const hours = Math.floor((totalSeconds % 86400) / 3600);
    const minutes = Math.floor((totalSeconds % 3600) / 60);
    const seconds = totalSeconds % 60;

    if (duration < 60 * 1000) {
      return `${seconds}`;
    } else if (duration < 3600 * 1000) {
      return `${String(minutes).padStart(2, '0')}:${String(seconds).padStart(
        2,
        '0',
      )}`;
    } else if (duration < 86400 * 1000) {
      return `${String(hours).padStart(2, '0')}:${String(minutes).padStart(
        2,
        '0',
      )}:${String(seconds).padStart(2, '0')}`;
    } else {
      return `${String(days).padStart(2, '0')}:${String(hours).padStart(
        2,
        '0',
      )}:${String(minutes).padStart(2, '0')}:${String(seconds).padStart(
        2,
        '0',
      )}`;
    }
  }, [remaining, duration]);

  return {
    remaining,
    formatted,
    isRunning,
    start,
    stop,
    reset,
  };
}
