import { SectionList, View } from 'react-native';
import { styles } from './styles';
import { useTranslation } from 'react-i18next';
import { KeyboardAwareScrollView } from 'react-native-keyboard-controller';
import { initialValues } from './model.tsx';
import {
  Text,
  BottomSelector,
  Button,
  BottomSelectorOption,
  WheelDatePicker,
} from 'tradewize-component';
import { Formik, FormikProps } from 'formik';
import { keyboardOffset } from '../../global/styles.ts';
import { useEffect, useMemo, useRef, useState } from 'react';
import Item from './component/Item.tsx';
import { getEnterYourInformationSchema } from '../../validators/register.ts';
import { useDispatch, useSelector } from 'react-redux';
import {
  getStatesAsync,
  hideDatePickerModal,
  setDateBirthOfUser,
} from '../../redux/features/register/actions.ts';
import { RootState } from '../../redux/store/types.ts';
import {
  getUpdatedSections,
  handleSubmitInformation,
} from '../../helper/register.ts';
import dayjs from 'dayjs';

const EnterYourInformation = () => {
  const { t } = useTranslation();
  const dispatch = useDispatch();
  const formRef = useRef<FormikProps<typeof initialValues>>(null);

  const getStatesData = useSelector(
    (state: RootState) => state.register.getStates.data,
  );
  const datePickerModal = useSelector(
    (state: RootState) => state.register.datePickerModal,
  );

  const [isSelectBoxVisible, setIsSelectBoxVisible] = useState(false);
  const [selectedOption, setSelectedOption] = useState<BottomSelectorOption[]>(
    [],
  );
  const [selectedField, setSelectedField] = useState<string>('');
  const [selectedOptionValue, setSelectedOptionValue] = useState<{
    [key: string]: string;
  }>({});

  // eslint-disable-next-line react-hooks/exhaustive-deps
  const validationSchema = useMemo(() => getEnterYourInformationSchema(), [t]);

  const renderSectionHeader = (section: { title: string }) => {
    return (
      <View style={styles.sectionHeader}>
        <Text style={styles.title}>{t(section.title)}</Text>
      </View>
    );
  };

  useEffect(() => {
    dispatch(getStatesAsync());
  }, [dispatch]);

  const finalSectionsListItems = useMemo(
    () =>
      getUpdatedSections(
        getStatesData?.map(state => ({
          value: state?.value,
          label: state?.label,
        })),
      ),
    [getStatesData],
  );

  return (
    <View style={styles.container}>
      <Formik
        innerRef={formRef}
        initialValues={initialValues}
        validationSchema={validationSchema}
        onSubmit={handleSubmitInformation}
      >
        {({
          values,
          handleChange,
          errors,
          touched,
          setTouched,
          setFieldValue,
        }) => (
          <SectionList
            renderScrollComponent={props => (
              <KeyboardAwareScrollView
                bottomOffset={keyboardOffset}
                {...props}
              />
            )}
            sections={finalSectionsListItems}
            renderItem={({ item, index, section }) => {
              return (
                <View style={styles.sectionItemContainer}>
                  <Item
                    item={item}
                    values={values}
                    handleChange={(field: string) => (value: string) =>
                      handleChange(field)(value)}
                    setFieldValue={setFieldValue}
                    errors={errors}
                    touched={touched}
                    setTouched={setTouched}
                    setSelectedOption={setSelectedOption}
                    setSelectedField={setSelectedField}
                    selectedOptionValue={selectedOptionValue}
                    setIsSelectBoxVisible={setIsSelectBoxVisible}
                    formRef={
                      formRef as React.RefObject<
                        FormikProps<typeof initialValues>
                      >
                    }
                  />
                  {index + 1 === section?.data?.length &&
                    section?.title !== finalSectionsListItems.at(-1)?.title && (
                      <View style={styles.sectionItemSeparator} />
                    )}
                </View>
              );
            }}
            renderSectionHeader={({ section: { title } }) =>
              renderSectionHeader({ title })
            }
            keyExtractor={(_item, index) => index.toString()}
          />
        )}
      </Formik>

      <Button
        size="large"
        style={styles.buttonSubmit}
        title={t('NEXT')}
        onPress={() => formRef?.current?.handleSubmit()}
      />

      <BottomSelector
        title={t('SELECT_AN_OPTION')}
        isVisible={isSelectBoxVisible}
        onClose={() => {
          setIsSelectBoxVisible(false);
        }}
        selectedValue={selectedOptionValue[selectedField]}
        options={selectedOption}
        onSelect={option => {
          formRef.current?.setFieldValue(selectedField, option?.label);
          setSelectedOptionValue({
            ...selectedOptionValue,
            [selectedField]: option.label,
          });
        }}
        buttonCancelStyle={styles.buttonCancerSelect}
        buttonCancelTextStyle={styles.buttonCancerSelectText}
        cancelText={t('CLOSE')}
        emptyText={t('NO_DATA_FOUND')}
      />

      <WheelDatePicker
        initialDate={
          datePickerModal?.date
            ? dayjs(datePickerModal?.date).toDate()
            : dayjs().subtract(18, 'year').toDate()
        }
        maxDate={new Date()}
        isVisible={datePickerModal.isVisible}
        title={t('SELECT_A_DATE')}
        confirmButtonText={t('CONFIRM')}
        cancelButtonText={t('CANCEL')}
        cancelButtonStyle={styles.cancelButtonWheelDatePicker}
        confirmButtonStyle={styles.confirmButtonWheelDatePicker}
        cancelButtonTextStyle={styles.cancelButtonTextWheelDatePicker}
        helperText={t(
          'YOU_NEED_ATTENTION_TO_BE_AT_LEAST_18_YEAR_OLD_TO_REGISTER',
        )}
        confirmButtonOnPress={(date: string) => {
          if (!date) {
            dispatch(
              setDateBirthOfUser(dayjs().subtract(18, 'year').toISOString()),
            );
          } else {
            const [month, day, year] = date.split('/').map(Number);
            const newDate = new Date(year, month - 1, day);
            dispatch(setDateBirthOfUser(dayjs(newDate).toDate().toISOString()));
          }
          dispatch(hideDatePickerModal());
        }}
        titleStyle={styles.titleWheelDatePicker}
        cancelButtonOnPress={() => {
          dispatch(hideDatePickerModal());
        }}
      />
    </View>
  );
};

export default EnterYourInformation;
