import { View } from 'react-native';
import { SelectBoxProps } from '../model';
import { useTranslation } from 'react-i18next';
import { styles } from '../styles';
import { Button, Text } from 'tradewize-component';
import { IconChevronDown } from '../../../assets/svg';
import { SvgXml } from 'react-native-svg';
import { globalStyles } from '../../../global/styles';

export const SelectBox = (props: SelectBoxProps) => {
  const {
    item,
    rowLayout,
    setSelectedOption,
    setSelectedField,
    selectedOptionValue,
    setIsSelectBoxVisible,
    errors,
    touched,
    fieldBasePath,
    formRef,
  } = props;
  const { t } = useTranslation();

  const fieldName = 'label' in item ? item?.label : '';
  const hasError = !!(
    errors?.[fieldName as keyof typeof errors] &&
    touched?.[fieldName as keyof typeof touched]
  );

  return (
    <View
      style={[
        styles.selectBoxContainer,
        rowLayout ? styles.rowLayoutSelectBox : styles.textFieldContainer,
      ]}
    >
      <Text style={[styles.labelField, styles.selectBoxLabelField]}>
        {t('label' in item ? item?.label : '')}
        {('isRequired' in item ? item?.isRequired : false) && (
          <Text style={styles.requiredField}>(*)</Text>
        )}
      </Text>
      <Button
        style={[
          styles.selectBoxButton,
          hasError && styles.selectBoxButtonError,
        ]}
        rightIcon={<SvgXml xml={IconChevronDown()} width={24} height={24} />}
        onPress={() => {
          setSelectedOption(
            'type' in item && item.type === 'select' && 'options' in item
              ? item.options
              : [],
          );
          setSelectedField('label' in item ? item?.label : '');

          // Mark field as touched when opened
          if (fieldBasePath && fieldName) {
            formRef?.current?.setFieldTouched(
              `${fieldBasePath}.${fieldName}`,
              true,
              true,
            );
          }

          setTimeout(() => {
            setIsSelectBoxVisible(true);
          }, 150);
        }}
        title={
          selectedOptionValue['label' in item ? item?.label : '']
            ? selectedOptionValue['label' in item ? item?.label : '']
            : t('SELECT_AN_OPTION')
        }
        textStyle={
          selectedOptionValue['label' in item ? item?.label : '']
            ? styles.selectBoxButtonText
            : styles.selectBoxButtonTextDefault
        }
      />
      {hasError && (
        <Text numberOfLines={2} style={globalStyles.errorText}>
          {errors?.[fieldName as keyof typeof errors]}
        </Text>
      )}
    </View>
  );
};
