import i18next from 'i18next';
import * as Yup from 'yup';
import { FORMAT_PHONE_NO } from '../utils';
import { photoIdentificationOptions } from '../screens/EnterYourInformation/model';
import dayjs from 'dayjs';

export const getEnterYourInformationSchema = () =>
  Yup.object().shape({
    FIRST_NAME: Yup.string()
      .required(i18next.t('FIRST_NAME_CANNOT_BE_EMPTY'))
      .matches(
        /^[A-Za-z]+$/,
        i18next.t('FIELD_INVALID_FORMAT', {
          field: i18next.t('FIRST_NAME'),
          format: i18next.t('REGEX_ONLY_ALLOW_LETTERS').toLocaleLowerCase(),
        }),
      )
      .min(
        3,
        i18next.t('FIELD_MIN_LENGTH', {
          field: i18next.t('FIRST_NAME'),
          number: 3,
        }),
      )
      .max(
        150,
        i18next.t('FIELD_MAX_LENGTH', {
          field: i18next.t('FIRST_NAME'),
          number: 150,
        }),
      ),
    LAST_NAME: Yup.string()
      .required(i18next.t('LAST_NAME_CANNOT_BE_EMPTY'))
      .matches(
        /^[A-Za-z]+$/,
        i18next.t('FIELD_INVALID_FORMAT', {
          field: i18next.t('LAST_NAME'),
          format: i18next.t('REGEX_ONLY_ALLOW_LETTERS').toLocaleLowerCase(),
        }),
      )
      .min(
        3,
        i18next.t('FIELD_MIN_LENGTH', {
          field: i18next.t('LAST_NAME'),
          number: 3,
        }),
      )
      .max(
        150,
        i18next.t('FIELD_MAX_LENGTH', {
          field: i18next.t('LAST_NAME'),
          number: 150,
        }),
      ),
    PHONE_NO: Yup.string()
      .required(i18next.t('PHONE_NO_CANNOT_BE_EMPTY'))
      .matches(
        /^\d{3}-\d{3}-\d{4}$/,
        i18next.t('PHONE_NO_INVALID_FORMAT', {
          format: FORMAT_PHONE_NO,
        }),
      )
      .test('len', i18next.t('PHONE_NO_MIN_LENGTH'), value => {
        if (!value) return false;
        const digitsOnly = value.replace(/\D/g, '');
        return digitsOnly.length === 10;
      }),
    EMAIL: Yup.string()
      .email(i18next.t('INVALID_EMAIL'))
      .required(i18next.t('EMAIL_CANNOT_BE_EMPTY')),
    STREET_ADDRESS: Yup.string().required(
      i18next.t('STREET_ADDRESS_CANNOT_BE_EMPTY'),
    ),
    CITY: Yup.string().required(i18next.t('CITY_CANNOT_BE_EMPTY')),
    STATE: Yup.string().required(i18next.t('STATE_CANNOT_BE_EMPTY')),
    ZIP_CODE: Yup.string().required(i18next.t('ZIP_CODE_CANNOT_BE_EMPTY')),
    REFERRAL: Yup.string().required(i18next.t('REFERRAL_CANNOT_BE_EMPTY')),
    FRIEND_NAME: Yup.lazy((_, { parent }) => {
      if (
        [i18next.t('FRIEND'), i18next.t('OTHER')].includes(
          parent.REFERRAL as unknown as string,
        )
      ) {
        return Yup.string().required(i18next.t('FRIEND_NAME_CANNOT_BE_EMPTY'));
      }
      return Yup.string().notRequired();
    }),
    DATE_OF_BIRTH: Yup.string()
      .required(i18next.t('DATE_OF_BIRTH_CANNOT_BE_EMPTY'))
      .test(
        'is-18-or-older',
        i18next.t('YOU_NEED_ATTENTION_TO_BE_AT_LEAST_18_YEAR_OLD_TO_REGISTER'),
        value => {
          if (!value) return false;

          // Parse ng√†y sinh t·ª´ chu·ªói YYYY-MM-DD
          const dob = new Date(value);
          if (isNaN(dob.getTime())) return false; // kh√¥ng h·ª£p l·ªá

          const today = new Date();
          let age = today.getFullYear() - dob.getFullYear();

          // N·∫øu ch∆∞a t·ªõi sinh nh·∫≠t nƒÉm nay th√¨ tr·ª´ 1 tu·ªïi
          const hasBirthdayPassed =
            today.getMonth() > dob.getMonth() ||
            (today.getMonth() === dob.getMonth() &&
              today.getDate() >= dob.getDate());

          if (!hasBirthdayPassed) {
            age--;
          }

          return age >= 18;
        },
      ),
    IDENTIFICATION_TYPE: Yup.string()
      .required(i18next.t('IDENTIFICATION_TYPE_CANNOT_BE_EMPTY'))
      .oneOf(
        photoIdentificationOptions.map(opt => opt.label),
        i18next.t('INVALID_IDENTIFICATION_TYPE'),
      ),
    IDENTIFICATION_NUMBER: Yup.string()
      .required(i18next.t('IDENTIFICATION_NUMBER_CANNOT_BE_EMPTY'))
      .test(
        'validate-id-number',
        i18next.t('INVALID_IDENTIFICATION_NUMBER'),
        function (value) {
          const { IDENTIFICATION_TYPE } = this.parent;
          if (!value || !IDENTIFICATION_TYPE) return false;

          switch (IDENTIFICATION_TYPE) {
            case i18next.t('STATE_ID'):
            case i18next.t('DRIVER_LICENSE'): {
              const regex = /^[A-Za-z0-9]{5,20}$/;
              if (!regex.test(value)) {
                return this.createError({ message: i18next.t('STATE_DES') });
              }
              break;
            }

            case i18next.t('PASSPORT'): {
              const regex = /^[A-Za-z0-9]+$/;
              if (!regex.test(value)) {
                return this.createError({ message: i18next.t('PASSPORT_DES') });
              }
              break;
            }

            case i18next.t('VOTER_ID'): {
              const regex = /^[A-Za-z0-9]{6,12}$/;
              if (!regex.test(value)) {
                return this.createError({ message: i18next.t('VOTER_ID_DES') });
              }
              break;
            }

            case i18next.t('OTHER'): {
              const regex = /^[A-Za-z0-9\s]{3,25}$/;
              if (!regex.test(value)) {
                return this.createError({ message: i18next.t('OTHER_DES') });
              }
              break;
            }

            default:
              return this.createError({
                message: i18next.t('INVALID_IDENTIFICATION_TYPE'),
              });
          }

          return true;
        },
      ),
    PHOTO_IDENTIFICATION: Yup.object()
      .shape({
        uri: Yup.string().required(),
      })
      .required(i18next.t('PHOTO_IDENTIFICATION_CANNOT_BE_EMPTY')),
  });

export const getShareYourExperienceSchema = () =>
  Yup.object().shape({
    companyInformation: Yup.array().of(
      Yup.object().shape({
        COMPANY_NAME: Yup.string().required(
          i18next.t('COMPANY_NAME_CANNOT_BE_EMPTY'),
        ),
        COMPANY_ADDRESS: Yup.string().required(
          i18next.t('COMPANY_ADDRESS_CANNOT_BE_EMPTY'),
        ),
        START_DATE: Yup.string().required(
          i18next.t('START_DATE_CANNOT_BE_EMPTY'),
        ),
        END_DATE: Yup.string()
          .required(i18next.t('END_DATE_CANNOT_BE_EMPTY'))
          .test(
            'endDateCheck',
            i18next.t('END_DATE_MUST_BE_AFTER_START_DATE'),
            function (value) {
              const { START_DATE } = this.parent;
              if (!value || !START_DATE) return true;

              const start = dayjs(START_DATE, 'MM-YYYY');
              const end = dayjs(value, 'MM-YYYY');

              return end.isSame(start) || end.isAfter(start);
            },
          ),
        ROLE: Yup.string().required(i18next.t('ROLE_CANNOT_BE_EMPTY')),
        JOB_DESCRIPTION: Yup.string().required(
          i18next.t('JOB_DESCRIPTION_CANNOT_BE_EMPTY'),
        ),
        PROJECTS: Yup.array().of(
          Yup.object().shape({
            COMPANY_PROJECT_NAME: Yup.string().required(
              i18next.t('COMPANY_PROJECT_NAME_CANNOT_BE_EMPTY'),
            ),
            COMPANY_PROJECT_DESCRIPTION: Yup.string().required(
              i18next.t('COMPANY_PROJECT_DESCRIPTION_CANNOT_BE_EMPTY'),
            ),
          }),
        ),
      }),
    ),
  });

export const getWorkReferencesSchema = () =>
  Yup.object().shape({
    references: Yup.array().of(
      Yup.object().shape({
        COMPANY_NAME: Yup.string().required(
          i18next.t('COMPANY_NAME_CANNOT_BE_EMPTY'),
        ),
        COMPANY_ADDRESS: Yup.string().required(
          i18next.t('COMPANY_ADDRESS_CANNOT_BE_EMPTY'),
        ),
        REFERENCE_NAME: Yup.string().required(
          i18next.t('ENTER_YOUR_REFERENCE_NAME'),
        ),
        REFERENCE_PHONE: Yup.string()
          .required(i18next.t('ENTER_YOUR_REFERENCE_PHONE'))
          .matches(
            /^\d{3}-\d{3}-\d{4}$/,
            i18next.t('PHONE_NO_INVALID_FORMAT', {
              format: FORMAT_PHONE_NO,
            }),
          )
          .test('len', i18next.t('PHONE_NO_MIN_LENGTH'), value => {
            if (!value) return false;
            const digitsOnly = value.replace(/\D/g, '');
            return digitsOnly.length === 10;
          }),
        REFERENCE_EMAIL: Yup.string()
          .email(i18next.t('INVALID_EMAIL'))
          .required(i18next.t('ENTER_YOUR_REFERENCE_EMAIL')),
        RELATIONSHIP: Yup.string().required(
          i18next.t('ENTER_YOUR_RELATIONSHIP'),
        ),
      }),
    ),
  });
