import { FlatList, Image, Text, View } from 'react-native';
import { styles } from './styles';
import { onboarding_process } from '../../assets/images';
import { useTranslation } from 'react-i18next';
import {
  IconBank,
  IconLight,
  IconRefer,
  IconRequest,
  IconWork,
} from '../../assets/svg';
import { SvgXml } from 'react-native-svg';
import { Button } from 'tradewize-component';
import { NavigationService } from '../../services/NavigationService';
interface ListItem {
  id: string;
  title: string;
  description: string;
  icon: string;
}

const LIST_ITEMS: ListItem[] = [
  {
    id: '1',
    title: 'ENTER_YOUR',
    description: 'INFORMATION',
    icon: IconRequest(),
  },
  {
    id: '2',
    title: 'GIVE_YOUR',
    description: 'EXPERIENCE',
    icon: IconWork(),
  },
  {
    id: '3',
    title: 'SUBMIT',
    description: 'REFERENCES',
    icon: IconRefer(),
  },
  {
    id: '4',
    title: 'FINDER',
    description: 'KNOWLEDGE',
    icon: IconLight(),
  },
  {
    id: '5',
    title: 'ACCOUNT',
    description: 'VERIFY',
    icon: IconBank(),
  },
];

const OnboardingProcess = () => {
  const { t } = useTranslation();

  const renderItem = ({ item }: { item: ListItem }) => {
    return (
      <View
        style={[
          styles.itemContainer,
          Number(item.id) % 2 === 0 && styles.itemIndented,
        ]}
      >
        <View style={styles.circleNumber}>
          <Text style={styles.circleNumberText}>0{item.id}</Text>
        </View>
        <View style={styles.lineSeparator} />

        <View style={styles.itemContent}>
          <SvgXml
            style={styles.itemIcon}
            xml={item.icon}
            width={24}
            height={24}
          />
          {Number(item.id) !== 4 && (
            <Text style={styles.text}>{t(item.title)}</Text>
          )}
          <Text style={[styles.text, styles.textActive]}>
            {t(item.description)}
          </Text>
          {Number(item.id) === 4 && (
            <Text style={styles.text}>{t(item.title)}</Text>
          )}
        </View>
      </View>
    );
  };

  return (
    <View style={styles.container}>
      <Image
        source={onboarding_process}
        style={styles.image}
        resizeMode="contain"
      />

      <View style={styles.contentContainer}>
        <FlatList
          scrollEnabled={false}
          contentContainerStyle={styles.contentListContainer}
          data={LIST_ITEMS}
          renderItem={renderItem}
          keyExtractor={item => item.id.toString()}
        />
      </View>

      <Button
        variant="primary"
        size="large"
        style={styles.button}
        onPress={() => NavigationService.navigate('EnterYourInformationScreen')}
      >
        <Text style={styles.buttonText}>{t('GO_TO_STEP', { number: 1 })}</Text>
      </Button>
    </View>
  );
};

export default OnboardingProcess;
