import { FormikProps } from 'formik';
import { FieldItem } from '../EnterYourInformation/model';
import InputDate from './components/InputDate';
import { BottomSelectorOption } from 'tradewize-component';
import { generateUUID } from '../../utils';

export const KEY_COMPANY_INFORMATION = 'companyInformation';

export interface SelectBoxProps {
  item: FieldItem;
  rowLayout?: boolean;
  setSelectedOption: (options: BottomSelectorOption[]) => void;
  setSelectedField: (field: string) => void;
  selectedOptionValue: any;
  setIsSelectBoxVisible: (visible: boolean) => void;
  errors?: any;
  touched?: any;
  fieldBasePath?: string;
  formRef?: React.RefObject<FormikProps<typeof initialValues>> | null;
}

export interface RenderItemProps {
  item: FieldItem;
  values: (typeof initialValues)[typeof KEY_COMPANY_INFORMATION][number];
  handleChange: (field: string) => (value: string) => void;
  errors: any;
  touched: any;
  setTouched: (touched: Record<string, boolean>) => void;
}

export interface RenderItemCompanyInformationProps {
  item: FieldItem;
  values: (typeof initialValues)[typeof KEY_COMPANY_INFORMATION] | unknown;
  handleChange: (field: string) => (value: string) => void;
  errors: any;
  touched: any;
  setTouched: (touched: Record<string, boolean>) => void;
}

export interface RenderItemCompanyProjectProps {
  item: FieldItem;
  values:
    | (typeof initialValues)[typeof KEY_COMPANY_INFORMATION][number]
    | unknown;
  handleChange: (field: string) => (value: string) => void;
  errors: any;
  touched: any;
  setTouched: (touched: Record<string, boolean>) => void;
}

export interface ItemProps {
  setSelectedOption: (options: BottomSelectorOption[]) => void;
  setSelectedField: (field: string) => void;
  selectedOptionValue: any;
  setIsSelectBoxVisible: (visible: boolean) => void;
  formRef: React.RefObject<FormikProps<typeof initialValues>> | null;
}

