import { Keyboard, TouchableWithoutFeedback, View } from 'react-native';
import { styles } from './styles';
import {
  BottomSelector,
  BottomSelectorOption,
  Button,
} from 'tradewize-component';
import { KeyboardAwareScrollView } from 'react-native-keyboard-controller';
import { initialValues } from './model';
import { useTranslation } from 'react-i18next';
import { Formik, FormikProps } from 'formik';
import { useMemo, useRef, useState } from 'react';
import { globalStyles } from '../../global/styles';
import { FieldItem } from '../EnterYourInformation/model';
import { ScrollView } from 'react-native-gesture-handler';

import CompanyInformation from './components/CompanyInformation';
import { getShareYourExperienceSchema } from '../../validators/register';
import {
  handleSubmitExperience,
  tabbarRegisterRef,
} from '../../helper/register';
import { generateUUID } from '../../utils';

const ShareYourExperience = () => {
  const { t } = useTranslation();
  const formRef = useRef<FormikProps<typeof initialValues>>(null);
  const formRefCompanyInformation = useRef<any>(null);

  const [isSelectBoxVisible, setIsSelectBoxVisible] = useState(false);
  const [selectedOption, setSelectedOption] = useState<BottomSelectorOption[]>(
    [],
  );
  const [selectedField, setSelectedField] = useState<string>('');
  const [selectedOptionValue, setSelectedOptionValue] = useState<{
    [key: string]: string;
  }>({});

  // eslint-disable-next-line react-hooks/exhaustive-deps
  const validationSchema = useMemo(() => getShareYourExperienceSchema(), [t]);

  return (
    <View style={globalStyles.flex}>
      <KeyboardAwareScrollView
        ScrollViewComponent={ScrollView}
        style={styles.container}
        bottomOffset={48}
      >
        <Formik
          innerRef={formRef}
          initialValues={initialValues}
          validationSchema={validationSchema}
          onSubmit={values => {
            handleSubmitExperience(values);
          }}
        >
          {({ values, errors, touched, setTouched }) => {
            return (
              <TouchableWithoutFeedback onPress={() => Keyboard.dismiss()}>
                <View style={globalStyles.flex}>
                  <CompanyInformation
                    item={undefined as unknown as FieldItem}
                    values={values.companyInformation}
                    errors={errors.companyInformation as any}
                    touched={touched.companyInformation as any}
                    setTouched={setTouched}
                    setSelectedOption={setSelectedOption}
                    setSelectedField={setSelectedField}
                    selectedOptionValue={selectedOptionValue}
                    setIsSelectBoxVisible={setIsSelectBoxVisible}
                    formRef={
                      formRef as React.RefObject<
                        FormikProps<typeof initialValues>
                      >
                    }
                    formRefCompanyInformation={formRefCompanyInformation}
                  />
                </View>
              </TouchableWithoutFeedback>
            );
          }}
        </Formik>
      </KeyboardAwareScrollView>

      <Button
        style={styles.buttonAddCompanyInformation}
        variant="ghost"
        size="small"
        textStyle={styles.buttonAddCompanyInformationText}
        title={t('ADD_ANOTHER_COMPANY')}
        onPress={() => {
          formRefCompanyInformation.current?.({
            ID: generateUUID(),
            COMPANY_NAME: '',
            COMPANY_ADDRESS: '',
            START_DATE: '',
            END_DATE: '',
            ROLE: '',
            JOB_DESCRIPTION: '',
            PROJECTS: [],
          });
        }}
      />

      <View style={styles.groupButtonContainer}>
        <Button
          variant="secondary"
          style={[styles.buttonFullWidth, styles.buttonCancel]}
          title={t('BACK')}
          textStyle={styles.buttonCancelText}
          onPress={() => {
            tabbarRegisterRef.current?.pressPrevious();
          }}
        />
        <Button
          variant="primary"
          style={[styles.buttonFullWidth, styles.buttonNext]}
          title={t('NEXT')}
          onPress={() => {
            formRef.current?.handleSubmit();
          }}
        />
      </View>

      <BottomSelector
        title={t('SELECT_AN_OPTION')}
        isVisible={isSelectBoxVisible}
        onClose={() => {
          setIsSelectBoxVisible(false);
        }}
        selectedValue={selectedOptionValue[selectedField]}
        options={selectedOption}
        onSelect={option => {
          formRef.current?.setFieldValue(
            selectedField,
            option.label.toString(),
          );
          setSelectedOptionValue({
            ...selectedOptionValue,
            [selectedField]: option.value,
          });
        }}
        buttonCancelStyle={styles.buttonCancerSelect}
        buttonCancelTextStyle={styles.buttonCancerSelectText}
        cancelText={t('CLOSE')}
        emptyText={t('NO_DATA_FOUND')}
      />
    </View>
  );
};

export default ShareYourExperience;
