import { StyleProp, View, ViewStyle } from 'react-native';
import { styles } from '../styles';
import { BottomSelectorOption, Button, Text } from 'tradewize-component';
import { useTranslation } from 'react-i18next';
import { SvgXml } from 'react-native-svg';
import { FieldItem } from '../model';
import { IconChevronDown } from '../../../assets/svg';
import { globalStyles } from '../../../global/styles';
import { FormikErrors, FormikTouched } from 'formik';
import colors from '../../../theme/colors';

interface SelectBoxProps {
  item: FieldItem;
  rowLayout?: boolean;
  setSelectedOption: (options: BottomSelectorOption[]) => void;
  setSelectedField: (field: string) => void;
  selectedOptionValue: any;
  setIsSelectBoxVisible: (visible: boolean) => void;
  errors: FormikErrors<Record<string, string>>;
  touched: FormikTouched<Record<string, string>>;
  setTouched: (touched: Record<string, boolean>) => void;
  style?: StyleProp<ViewStyle>;
}

const SelectBox = (props: SelectBoxProps) => {
  const {
    touched,
    errors,
    item,
    rowLayout,
    setSelectedOption,
    setSelectedField,
    setTouched,
    selectedOptionValue,
    setIsSelectBoxVisible,
    style,
  } = props;
  const { t } = useTranslation();

  return (
    <View
      style={[
        rowLayout ? styles.rowLayoutSelectBox : styles.textFieldContainer,
        style,
      ]}
    >
      <Text style={[styles.labelField, styles.selectBoxLabelField]}>
        {t('label' in item ? item?.label : '')}{' '}
        {('isRequired' in item ? item?.isRequired : false) && (
          <Text style={styles.requiredField}>(*)</Text>
        )}
      </Text>
      <Button
        style={[
          styles.selectBoxButton,
          errors &&
            errors['label' in item ? item?.label : ''] &&
            touched &&
            touched['label' in item ? item?.label : ''] && {
              borderColor: colors.red,
            },
        ]}
        rightIcon={<SvgXml xml={IconChevronDown()} width={24} height={24} />}
        onPress={() => {
          setSelectedOption(
            'type' in item && item.type === 'select' && 'options' in item
              ? item.options
              : [],
          );
          setSelectedField('label' in item ? item?.label : '');

          setTimeout(() => {
            setIsSelectBoxVisible(true);
            setTouched({
              ...touched,
              ['label' in item ? item?.label : '']: true,
            } as Record<string, boolean>);
          }, 150);
        }}
        title={
          selectedOptionValue['label' in item ? item?.label : '']
            ? selectedOptionValue['label' in item ? item?.label : '']
            : t('SELECT_AN_OPTION')
        }
        textStyle={
          selectedOptionValue['label' in item ? item?.label : '']
            ? styles.selectBoxButtonText
            : styles.selectBoxButtonTextDefault
        }
      />
      {errors &&
        errors['label' in item ? item?.label : ''] &&
        touched &&
        touched['label' in item ? item?.label : ''] && (
          <Text numberOfLines={2} style={globalStyles.errorText}>
            {errors['label' in item ? item?.label : '']}
          </Text>
        )}
    </View>
  );
};

export default SelectBox;
