import { FormikErrors, FormikProps, FormikTouched } from 'formik';
import { View } from 'react-native';
import { FieldItem, initialValues } from '../model';
import { BottomSelectorOption, Input, Text } from 'tradewize-component';
import { styles } from '../styles';
import SelectBox from './SelectBox';
import { globalStyles } from '../../../global/styles';
import { useTranslation } from 'react-i18next';
import colors from '../../../theme/colors';
import i18next from 'i18next';

interface ItemProps {
  item: FieldItem;
  values: typeof initialValues;
  handleChange: (field: string) => (value: string) => void;
  errors: FormikErrors<Record<string, string>>;
  touched: FormikTouched<Record<string, string>>;
  setTouched: (touched: Record<string, boolean>) => void;
  setSelectedOption: (options: BottomSelectorOption[]) => void;
  setSelectedField: (field: string) => void;
  selectedOptionValue: any;
  setIsSelectBoxVisible: (visible: boolean) => void;
  formRef: React.RefObject<FormikProps<typeof initialValues>> | null;
  setFieldValue: (field: string, value: any) => void;
}

const ARR_KEYBOARD_NUMBER = ['PHONE_NO', 'ZIP_CODE'];
const ARR_MAX_LENGTH = [
  { FIRST_NAME: 150 },
  { LAST_NAME: 150 },
  { PHONE_NO: 12 },
  { ZIP_CODE: 5 },
];
const ARR_FRIEND_NAME = [i18next.t('FRIEND'), i18next.t('OTHER')];

const getMaxLength = (fieldName: string) => {
  const item = ARR_MAX_LENGTH.find(obj => {
    return fieldName in obj;
  });
  return item ? item[fieldName as keyof typeof item] : undefined;
};

const Item = (props: ItemProps) => {
  const { t } = useTranslation();
  const {
    item,
    values,
    setFieldValue,
    errors,
    touched,
    setTouched,
    setSelectedOption,
    setSelectedField,
    selectedOptionValue,
    setIsSelectBoxVisible,
    formRef,
  } = props;

  if ('field' in item) {
    return (
      <View style={styles.groupFieldContainer}>
        {item.field.map((field, index) => {
          if ('type' in field && field?.type === 'select') {
            return (
              <View
                key={index.toString()}
                style={styles.selectBoxContainerField}
              >
                <SelectBox
                  errors={errors}
                  touched={touched}
                  setTouched={setTouched}
                  item={field}
                  rowLayout={true}
                  setSelectedOption={setSelectedOption}
                  setSelectedField={setSelectedField}
                  selectedOptionValue={selectedOptionValue}
                  setIsSelectBoxVisible={setIsSelectBoxVisible}
                />
              </View>
            );
          }
          return (
            <View key={index.toString()} style={styles.groupFieldItemContainer}>
              <Text style={styles.labelField}>
                {t('label' in field ? field?.label : '')}{' '}
                {('isRequired' in field ? field?.isRequired : false) && (
                  <Text style={styles.requiredField}>(*)</Text>
                )}
              </Text>

              <Input
                keyboardType={
                  ARR_KEYBOARD_NUMBER.includes(
                    'label' in field ? field?.label : '',
                  )
                    ? 'number-pad'
                    : 'default'
                }
                placeholder={t(
                  'placeholder' in field ? field?.placeholder : '',
                )}
                value={
                  (values as unknown as Record<string, string>)[
                    ('label' in field
                      ? field?.label
                      : '') as keyof typeof values
                  ] as string
                }
                style={styles.groupFieldItemInput}
                inputContainerStyle={styles.groupFieldItemInputContainer}
                onChangeText={(text: string) => {
                  if (
                    ARR_KEYBOARD_NUMBER.includes(
                      'label' in field ? field?.label : '',
                    )
                  ) {
                    const digitsOnly = text.replace(/\D/g, '');
                    let formatted = digitsOnly;
                    if (digitsOnly.length > 3 && digitsOnly.length <= 6) {
                      formatted = `${digitsOnly.slice(0, 3)}-${digitsOnly.slice(
                        3,
                      )}`;
                    } else if (digitsOnly.length > 6) {
                      formatted = `${digitsOnly.slice(0, 3)}-${digitsOnly.slice(
                        3,
                        6,
                      )}-${digitsOnly.slice(6, 10)}`;
                    }
                    setFieldValue(
                      'label' in field ? field?.label : '',
                      'label' in field && field?.label === 'PHONE_NO'
                        ? formatted
                        : digitsOnly,
                    );
                  } else {
                    setFieldValue('label' in field ? field?.label : '', text);
                  }
                }}
                isError={
                  !!(
                    errors['label' in field ? field?.label : ''] &&
                    touched['label' in field ? field?.label : '']
                  )
                }
                onFocus={() => {
                  setTouched({
                    ...touched,
                    ['label' in field ? field?.label : '']: false,
                  } as Record<string, boolean>);
                }}
                onBlur={() => {
                  setTouched({
                    ...touched,
                    ['label' in field ? field?.label : '']: true,
                  } as Record<string, boolean>);
                }}
                borderColorActive={colors.mainColorPrimary}
                borderColor={colors.grayScale40}
                placeholderTextColor={colors.grayScale70}
                maxLength={getMaxLength('label' in field ? field?.label : '')}
              />
              <View>
                {errors['label' in field ? field?.label : ''] &&
                  touched['label' in field ? field?.label : ''] && (
                    <Text numberOfLines={2} style={globalStyles.errorText}>
                      {errors['label' in field ? field?.label : '']}
                    </Text>
                  )}
              </View>
            </View>
          );
        })}
      </View>
    );
  }

  if ('type' in item && item.type === 'select') {
    return (
      <SelectBox
        setTouched={setTouched}
        errors={errors}
        touched={touched}
        item={item}
        rowLayout={false}
        setSelectedOption={setSelectedOption}
        setSelectedField={setSelectedField}
        selectedOptionValue={selectedOptionValue}
        setIsSelectBoxVisible={setIsSelectBoxVisible}
        style={styles.selectBoxContainerItem}
      />
    );
  }

  if ('type' in item && item.type === 'custom') {
    return (
      <View style={styles.customFieldContainer}>
        <Text style={styles.labelField}>
          {t('label' in item ? item?.label : '')}{' '}
          {('isRequired' in item ? item?.isRequired : false) && (
            <Text style={styles.requiredField}>(*)</Text>
          )}
        </Text>
        {'render' in item
          ? item?.render({
              errors: errors,
              touched: touched,
              setFieldValue: setFieldValue,
              setTouched: setTouched,
              onPress: (date: string) => {
                formRef?.current?.setFieldValue(
                  'label' in item ? item?.label : '',
                  date,
                );
              },
            })
          : null}
      </View>
    );
  }

  if (
    'label' in item &&
    item.label === 'FRIEND_NAME' &&
    !ARR_FRIEND_NAME?.includes(values?.REFERRAL)
  ) {
    return null;
  }

  return (
    <View style={styles.textFieldContainer}>
      <Text style={styles.labelField}>
        {t('label' in item ? item?.label : '')}{' '}
        {('isRequired' in item ? item?.isRequired : false) && (
          <Text style={styles.requiredField}>(*)</Text>
        )}
      </Text>
      <Input
        keyboardType={
          ARR_KEYBOARD_NUMBER.includes('label' in item ? item?.label : '')
            ? 'number-pad'
            : 'default'
        }
        placeholder={t('placeholder' in item ? item?.placeholder : '')}
        value={
          (values as unknown as Record<string, string>)[
            ('label' in item ? item?.label : '') as keyof typeof values
          ] as string
        }
        maxLength={
          ARR_KEYBOARD_NUMBER.includes('label' in item ? item?.label : '')
            ? 12
            : undefined
        }
        style={styles.groupFieldItemInput}
        inputContainerStyle={styles.groupFieldItemInputContainer}
        onChangeText={(text: string) => {
          if (
            ARR_KEYBOARD_NUMBER.includes('label' in item ? item?.label : '')
          ) {
            const digitsOnly = text.replace(/\D/g, '');
            let formatted = digitsOnly;
            if (digitsOnly.length > 3 && digitsOnly.length <= 6) {
              formatted = `${digitsOnly.slice(0, 3)}-${digitsOnly.slice(3)}`;
            } else if (digitsOnly.length > 6) {
              formatted = `${digitsOnly.slice(0, 3)}-${digitsOnly.slice(
                3,
                6,
              )}-${digitsOnly.slice(6, 10)}`;
            }
            setFieldValue('label' in item ? item?.label : '', formatted);
          } else {
            setFieldValue('label' in item ? item?.label : '', text);
          }
        }}
        isError={
          !!(
            errors['label' in item ? item?.label : ''] &&
            touched['label' in item ? item?.label : '']
          )
        }
        onFocus={() => {
          setTouched({
            ...touched,
            ['label' in item ? item?.label : '']: false,
          } as Record<string, boolean>);
        }}
        onBlur={() => {
          setTouched({
            ...touched,
            ['label' in item ? item?.label : '']: true,
          } as Record<string, boolean>);
        }}
        borderColorActive={colors.mainColorPrimary}
        borderColor={colors.grayScale40}
        placeholderTextColor={colors.grayScale70}
      />
      {errors['label' in item ? item?.label : ''] &&
        touched['label' in item ? item?.label : ''] && (
          <Text numberOfLines={2} style={globalStyles.errorText}>
            {errors['label' in item ? item?.label : '']}
          </Text>
        )}
    </View>
  );
};

export default Item;
