import { View } from 'react-native';
import {
  initialValues,
  KEY_COMPANY_INFORMATION,
  RenderItemCompanyProjectProps,
  RenderItemProps,
} from '../model';
import { useTranslation } from 'react-i18next';
import { SelectBox } from './SelectBox';
import { styles } from '../styles';
import { Input, Text } from 'tradewize-component';
import { globalStyles } from '../../../global/styles';
import colors from '../../../theme/colors';
import { ItemProps } from '../model';

const Item = (
  props: (RenderItemProps | RenderItemCompanyProjectProps) &
    ItemProps & { index?: number; fieldBasePath?: string },
) => {
  const { t } = useTranslation();
  const {
    // index,
    item,
    values,
    handleChange,
    errors,
    touched,
    setSelectedOption,
    setSelectedField,
    selectedOptionValue,
    setIsSelectBoxVisible,
    formRef,
    fieldBasePath = '',
  } = props;

  if ('field' in item) {
    return (
      <View style={styles.groupFieldContainer}>
        {item.field.map((field, i) => {
          if ('type' in field && field?.type === 'select') {
            return (
              <View key={i.toString()} style={globalStyles.flex}>
                <SelectBox
                  item={item}
                  rowLayout={true}
                  setSelectedOption={setSelectedOption}
                  setSelectedField={setSelectedField}
                  selectedOptionValue={selectedOptionValue}
                  setIsSelectBoxVisible={setIsSelectBoxVisible}
                  errors={errors}
                  touched={touched}
                  fieldBasePath={fieldBasePath}
                  formRef={formRef}
                />
              </View>
            );
          }
          if ('type' in field && field?.type === 'custom') {
            const customFieldName = 'label' in field ? field?.label : '';
            const hasCustomError = !!(
              errors?.[customFieldName as keyof typeof errors] &&
              touched?.[customFieldName as keyof typeof touched]
            );

            return (
              <View key={i.toString()} style={globalStyles.flex}>
                <Text style={styles.labelField}>
                  {t('label' in field ? field?.label : '')}
                  {('isRequired' in field ? field?.isRequired : false) && (
                    <Text style={styles.requiredField}>(*)</Text>
                  )}
                </Text>
                {'render' in field
                  ? field?.render({
                      onChange: (date: any) => {
                        console.log('date', date);

                        if (fieldBasePath && customFieldName) {
                          formRef?.current?.setFieldValue(
                            `${fieldBasePath}.${customFieldName}`,
                            date,
                          );
                          // Mark as touched and validate
                          formRef?.current?.setFieldTouched(
                            `${fieldBasePath}.${customFieldName}`,
                            true,
                            true,
                          );
                        }
                      },
                      onOpen: () => {
                        // Mark as touched when picker opens
                        if (fieldBasePath && customFieldName) {
                          formRef?.current?.setFieldTouched(
                            `${fieldBasePath}.${customFieldName}`,
                            true,
                            true,
                          );
                        }
                      },
                      hasError: hasCustomError,
                    })
                  : null}
                {hasCustomError && (
                  <Text numberOfLines={2} style={globalStyles.errorText}>
                    {errors?.[customFieldName as keyof typeof errors]}
                  </Text>
                )}
              </View>
            );
          }

          return (
            <View key={i.toString()} style={styles.groupFieldItemContainer}>
              <Text style={styles.labelField}>
                {t('label' in field ? field?.label : '')}
                {('isRequired' in field ? field?.isRequired : false) && (
                  <Text style={styles.requiredField}>(*)</Text>
                )}
              </Text>

              <Input
                placeholder={t(
                  'placeholder' in field ? field?.placeholder : '',
                )}
                value={
                  (
                    values as unknown as (typeof initialValues)[typeof KEY_COMPANY_INFORMATION][number]
                  )[
                    ('label' in field
                      ? field?.label
                      : '') as keyof (typeof initialValues)[typeof KEY_COMPANY_INFORMATION][number]
                  ] as string
                }
                style={styles.groupFieldItemInput}
                inputContainerStyle={styles.groupFieldItemInputContainer}
                onChangeText={handleChange(
                  'label' in field ? field?.label : '',
                )}
                isError={
                  !!(
                    errors?.['label' in field ? field?.label : ''] &&
                    touched?.['label' in field ? field?.label : '']
                  )
                }
                onFocus={() => {
                  const fieldName = 'label' in field ? field?.label : '';
                  if (fieldBasePath && fieldName) {
                    formRef?.current?.setFieldTouched(
                      `${fieldBasePath}.${fieldName}`,
                      false,
                      false,
                    );
                  }
                }}
                onBlur={() => {
                  const fieldName = 'label' in field ? field?.label : '';
                  if (fieldBasePath && fieldName) {
                    formRef?.current?.setFieldTouched(
                      `${fieldBasePath}.${fieldName}`,
                      true,
                      true,
                    );
                  }
                }}
                borderColorActive={colors.mainColorPrimary}
                borderColor={colors.grayScale40}
                placeholderTextColor={colors.grayScale70}
              />
            </View>
          );
        })}
      </View>
    );
  }

  if ('type' in item && item.type === 'select') {
    return (
      <SelectBox
        item={item}
        rowLayout={false}
        setSelectedOption={setSelectedOption}
        setSelectedField={setSelectedField}
        selectedOptionValue={selectedOptionValue}
        setIsSelectBoxVisible={setIsSelectBoxVisible}
        errors={errors}
        touched={touched}
        fieldBasePath={fieldBasePath}
        formRef={formRef}
      />
    );
  }

  if ('type' in item && item.type === 'custom') {
    const customFieldName = 'label' in item ? item?.label : '';
    const hasCustomError = !!(
      errors?.[customFieldName as keyof typeof errors] &&
      touched?.[customFieldName as keyof typeof touched]
    );

    return (
      <View style={styles.customFieldContainer}>
        <Text style={styles.labelField}>
          {t('label' in item ? item?.label : '')}
          {('isRequired' in item ? item?.isRequired : false) && (
            <Text style={styles.requiredField}>(*)</Text>
          )}
        </Text>
        {'render' in item
          ? item?.render({
              onPress: (date: string) => {
                if (fieldBasePath && customFieldName) {
                  formRef?.current?.setFieldValue(
                    `${fieldBasePath}.${customFieldName}`,
                    date,
                  );
                  // Mark as touched and validate
                  formRef?.current?.setFieldTouched(
                    `${fieldBasePath}.${customFieldName}`,
                    true,
                    true,
                  );
                }
              },
              onOpen: () => {
                // Mark as touched when picker opens
                if (fieldBasePath && customFieldName) {
                  formRef?.current?.setFieldTouched(
                    `${fieldBasePath}.${customFieldName}`,
                    true,
                    true,
                  );
                }
              },
              hasError: hasCustomError,
            })
          : null}
        {hasCustomError && (
          <Text numberOfLines={2} style={globalStyles.errorText}>
            {errors?.[customFieldName as keyof typeof errors]}
          </Text>
        )}
      </View>
    );
  }

  const fieldName = 'label' in item ? item?.label : '';
  const isTextarea = 'type' in item && item?.type === 'textarea';

  return (
    <View style={styles.textFieldContainer}>
      <Text style={styles.labelField}>
        {t('label' in item ? item?.label : '')}
        {('isRequired' in item ? item?.isRequired : false) && (
          <Text style={styles.requiredField}>(*)</Text>
        )}
      </Text>
      <Input
        multiline={isTextarea}
        placeholder={t('placeholder' in item ? item?.placeholder : '')}
        value={
          (
            values as unknown as (typeof initialValues)[typeof KEY_COMPANY_INFORMATION][number]
          )[
            ('label' in item ? item?.label : '') as keyof typeof values
          ] as string
        }
        style={styles.groupFieldItemInput}
        inputContainerStyle={[
          styles.groupFieldItemInputContainer,
          isTextarea && styles.groupFieldItemInputContainerTextarea,
        ]}
        onChangeText={handleChange(fieldName)}
        isError={
          !!(
            errors?.[fieldName as keyof typeof errors] &&
            touched?.[fieldName as keyof typeof touched]
          )
        }
        onFocus={() => {
          if (fieldBasePath && fieldName) {
            formRef?.current?.setFieldTouched(
              `${fieldBasePath}.${fieldName}`,
              false,
              false,
            );
          }
        }}
        onBlur={() => {
          if (fieldBasePath && fieldName) {
            formRef?.current?.setFieldTouched(
              `${fieldBasePath}.${fieldName}`,
              true,
              true,
            );
          }
        }}
        borderColorActive={colors.mainColorPrimary}
        borderColor={colors.grayScale40}
        placeholderTextColor={colors.grayScale70}
      />
      {errors?.[fieldName as keyof typeof errors] &&
        touched?.[fieldName as keyof typeof touched] && (
          <Text numberOfLines={2} style={globalStyles.errorText}>
            {errors?.[fieldName as keyof typeof errors]}
          </Text>
        )}
    </View>
  );
};

export default Item;
