import { SectionList, StyleSheet, View, Image } from 'react-native';
import {
  Button,
  CheckboxButton,
  SCREEN_WIDTH,
  Text,
} from 'tradewize-component';
import { RootState } from '../../../redux/store/types';
import { useSelector, useDispatch } from 'react-redux';
import colors from '../../../theme/colors';
import { useTranslation } from 'react-i18next';
import { StateData } from '../../../redux/features/register/types';
import { useMemo, useEffect } from 'react';
import {
  toggleQuestionAnswer,
  addQuestionAnswerImages,
  removeQuestionAnswerImage,
} from '../../../redux/features/register/actions';
import { logger } from '../../../utils';
import ImagePicker from 'react-native-image-crop-picker';

interface QuestionProps {
  onAnswersChange?: (answers: any[]) => void;
  itemRefs?: any;
}

const Question = ({ onAnswersChange, itemRefs }: QuestionProps) => {
  const dispatch = useDispatch();
  const { t } = useTranslation();
  const onboardingQuestionData = useSelector(
    (state: RootState) => state.register.getAllOnboardingQuestion,
  );
  const companyDropdownData = useSelector(
    (state: RootState) => state.register.getCompanyDropdown,
  );
  const questionAnswers = useSelector(
    (state: RootState) => state.register.questionAnswers,
  );
  const errorQuestion = useSelector(
    (state: RootState) => state.register.errorQuestion,
  );

  const finalOnboardingQuestionData = useMemo(() => {
    const questionSelected = companyDropdownData?.data
      ?.filter((element: StateData) => element?.isChecked)
      ?.map((element: StateData) => element?.key);

    return onboardingQuestionData?.data?.filter((element: any) => {
      return questionSelected?.includes(element?.id);
    });
  }, [companyDropdownData, onboardingQuestionData]);

  const isOptionSelected = (questionId: string, optionId: string) => {
    return (
      questionAnswers?.some(
        answer =>
          answer.questionId === questionId &&
          answer.optionId === optionId &&
          answer.isChecked,
      ) || false
    );
  };

  const getOptionImages = (questionId: string, optionId: string) => {
    const foundAnswer = questionAnswers?.find(
      answer =>
        answer.questionId === questionId &&
        answer.optionId === optionId &&
        answer.isChecked,
    );
    return foundAnswer?.images || [];
  };

  const handleOptionToggle = (questionId: string, optionId: string) => {
    dispatch(toggleQuestionAnswer({ questionId, optionId }));
  };

  const handleMedia = async (
    type: 'picker' | 'takePhoto',
    questionId: string,
    optionId: string,
  ) => {
    try {
      // Ki·ªÉm tra s·ªë l∆∞·ª£ng ·∫£nh hi·ªán t·∫°i
      const currentImages = getOptionImages(questionId, optionId);

      let images: any[] = [];
      const remainingSlots = 5 - currentImages.length;

      if (type === 'picker') {
        const result = await ImagePicker.openPicker({
          multiple: true,
          maxFiles: remainingSlots,
          mediaType: 'photo',
          forceJpg: true,
          includeBase64: false,
          includeExif: true,
        });
        images = Array.isArray(result) ? result : [result];
      } else {
        const result = await ImagePicker.openCamera({
          cropping: false,
          includeExif: true,
          mediaType: 'photo',
          forceJpg: true,
          includeBase64: false,
        });
        images = [result];
      }

      // L∆∞u th√¥ng tin ·∫£nh v√†o Redux
      const imageData = images.map(img => ({
        path: img.path,
        fileName: img.filename || `image_${Date.now()}.jpg`,
        typeFile: img.mime || 'image/jpeg',
      }));
      dispatch(
        addQuestionAnswerImages({ questionId, optionId, images: imageData }),
      );
    } catch (error) {
      logger('error handleMedia', error);
    }
  };

  useEffect(() => {
    if (onAnswersChange) {
      const selectedAnswers =
        questionAnswers?.filter(answer => answer.isChecked) || [];
      onAnswersChange(selectedAnswers);
    }
  }, [questionAnswers, onAnswersChange]);

  const renderItem = ({ item, index }: { item: any; index: number }) => {
    return (
      <View
        ref={ref => {
          if (ref) itemRefs.current[item?.id] = ref;
        }}
        style={styles.containerQuestionItem}
      >
        <View style={errorQuestion?.includes(item?.id) && styles.questionError}>
          <Text style={styles.titleQuestion}>
            {index + 1}. {item?.question}
          </Text>
        </View>
        {item?.options?.map((element: any) => {
          const isSelected = isOptionSelected(item?.id, element?.optionId);

          return (
            <View key={element?.optionId}>
              <CheckboxButton
                style={styles.checkboxButton}
                checkboxStyle={[
                  styles.checkboxButtonStyle,
                  isSelected && {
                    backgroundColor: colors.white,
                    borderColor: colors.mainColorPrimary,
                  },
                ]}
                customCheckmark={
                  isSelected ? <View style={styles.checkmarkBox} /> : null
                }
                label={element?.option}
                isChecked={isSelected}
                onChange={() => handleOptionToggle(item?.id, element?.optionId)}
              />
            </View>
          );
        })}

        {/* Hi·ªÉn th·ªã ·∫£nh v√† n√∫t upload cho c√°c option ƒë√£ ch·ªçn c√≥ requiredImage = true */}
        {item?.options?.map((element: any) => {
          const isSelected = isOptionSelected(item?.id, element?.optionId);
          const optionImages = getOptionImages(item?.id, element?.optionId);

          if (!isSelected || !element?.requiredImage) return null;

          return (
            <View key={`images-${element?.optionId}`}>
              {/* Hi·ªÉn th·ªã ·∫£nh ƒë√£ ch·ªçn cho option n√†y */}
              {optionImages?.length > 0 && (
                <View style={styles.imageContainer}>
                  <View style={styles.imageGrid}>
                    {optionImages.map((imageData: any, imageIndex: number) => (
                      <View key={imageIndex} style={styles.imageWrapper}>
                        <Image
                          source={{ uri: imageData.path }}
                          style={styles.selectedImage}
                        />
                        <Button
                          variant="ghost"
                          style={styles.removeImageButton}
                          onPress={() =>
                            dispatch(
                              removeQuestionAnswerImage({
                                questionId: item?.id,
                                optionId: element?.optionId,
                                imageIndex,
                              }),
                            )
                          }
                        >
                          <Text style={styles.removeImageText}>√ó</Text>
                        </Button>
                      </View>
                    ))}
                  </View>
                </View>
              )}

              {/* N√∫t upload ·∫£nh cho option ƒë√£ ch·ªçn */}
              {optionImages?.length === 0 && (
                <Text style={styles.titleRequiredImage}>
                  {t('ADD_REQUIRED_IMAGE_FOR_QUESTION', { number: 1 })}
                </Text>
              )}
              <View style={styles.groundButtonMedia}>
                <Button
                  size="small"
                  style={[
                    styles.buttonMedia,
                    (optionImages?.length || 0) >= 5 && styles.disabledButton,
                  ]}
                  title={t('UPLOAD_IMAGE')}
                  disabled={(optionImages?.length || 0) >= 5}
                  onPress={() =>
                    handleMedia('picker', item?.id, element?.optionId)
                  }
                />
                <Button
                  size="small"
                  style={[
                    styles.buttonMedia,
                    (optionImages?.length || 0) >= 5 && styles.disabledButton,
                  ]}
                  title={t('TAKE_PHOTO')}
                  disabled={(optionImages?.length || 0) >= 5}
                  onPress={() =>
                    handleMedia('takePhoto', item?.id, element?.optionId)
                  }
                />
              </View>
            </View>
          );
        })}
      </View>
    );
  };

  return (
    <View style={styles.containerQuestion}>
      <SectionList
        scrollEnabled={false}
        nestedScrollEnabled
        sections={finalOnboardingQuestionData || []}
        renderItem={renderItem}
        keyExtractor={item => item?.id}
        renderSectionHeader={({ section }) => {
          if (!section?.data || section?.data?.length <= 0) {
            return null;
          }
          return (
            <Text style={styles.titleSectionHeader}>
              {t('KNOWLEDGE_TEST_FOR', {
                companyName: companyDropdownData?.data?.find(
                  (item: any) => item?.key === section?.id,
                )?.value,
              })}
            </Text>
          );
        }}
      />
    </View>
  );
};

const styles = StyleSheet.create({
  titleRequiredImage: {
    fontSize: 12,
    fontWeight: '500',
    color: colors.red,
    lineHeight: 16,
    textAlign: 'center',
    marginTop: 12,
  },
  containerQuestion: {
    marginTop: 16,
  },
  titleQuestion: {
    fontSize: 16,
    fontWeight: '500',
    color: colors.black,
    lineHeight: 20,
    marginBottom: 8,
  },
  questionError: {
    backgroundColor: colors.alertBgError,
    borderRadius: 4,
    marginBottom: 4,
    padding: 4,
  },
  titleSectionHeader: {
    fontSize: 18,
    fontWeight: 'bold',
    color: colors.mainColorPrimary,
    lineHeight: 28,
    marginBottom: 8,
  },
  checkboxButton: {
    marginBottom: 4,
  },
  checkboxButtonStyle: {
    borderRadius: 100,
  },
  checkmarkBox: {
    width: 12,
    height: 12,
    borderRadius: 100,
    backgroundColor: colors.mainColorPrimary,
  },
  containerQuestionItem: {
    borderBottomWidth: 1,
    borderBottomColor: colors.line,
    marginBottom: 16,
    paddingBottom: 8,
  },
  groundButtonMedia: {
    flexDirection: 'row',
    gap: 8,
    alignItems: 'center',
  },
  disabledButton: {
    opacity: 0.5,
  },
  buttonMedia: {
    flex: 1,
    backgroundColor: colors.mainColorPrimary,
    marginTop: 8,
  },
  imageContainer: {
    marginTop: 8,
  },
  imageGrid: {
    flexDirection: 'row',
    flexWrap: 'wrap',
    gap: 8,
  },
  imageWrapper: {
    position: 'relative',
  },
  selectedImage: {
    width: SCREEN_WIDTH * 0.2 - 16,
    height: SCREEN_WIDTH * 0.2 - 16,
    borderRadius: 8,
    backgroundColor: colors.line,
  },
  removeImageButton: {
    position: 'absolute',
    top: -5,
    right: -5,
    borderRadius: 100,
    backgroundColor: colors.red,
    minHeight: 0,
    paddingVertical: 0,
    paddingHorizontal: 8,
  },
  removeImageText: {
    color: colors.white,
    fontSize: 14,
    fontWeight: 'bold',
  },
});

export default Question;
