import React, { useEffect, forwardRef, useImperativeHandle } from 'react';
import { View } from 'react-native';
import { styles } from './styles';
import { MaterialTopTabBarProps } from '@react-navigation/material-top-tabs';
import { Text } from 'tradewize-component';
import { useTranslation } from 'react-i18next';

export const TAB_ORDER = [
  'OnboardingProcessScreen',
  'EnterYourInformationScreen',
  'ShareYourExperienceScreen',
  'WorkReferencesScreen',
  'UnderstandingYourKnowledgeScreen',
  'VerifyAccountScreen',
];

interface TabbarOnboardingProps extends MaterialTopTabBarProps {
  onTabChange?: (routeName: string) => void;
}

const TabbarOnboarding = forwardRef(
  ({ state, onTabChange, navigation }: TabbarOnboardingProps, ref) => {
    const { t } = useTranslation();
    const visibleRoutes = state.routes.slice(1);

    // üîî Notify parent when active tab changes
    useEffect(() => {
      const activeRoute = state.routes[state.index];
      if (activeRoute && onTabChange) {
        onTabChange(activeRoute.name);
      }
    }, [state.index, state.routes, onTabChange]);

    // üß† Expose methods ra ngo√†i
    useImperativeHandle(ref, () => ({
      /** Chuy·ªÉn ƒë·∫øn tab c·ª• th·ªÉ */
      pressTab: (routeName: string) => {
        const route = state.routes.find(r => r.name === routeName);
        if (route) {
          navigation.navigate(route.name);
          onTabChange?.(route.name);
        }
      },
      /** Chuy·ªÉn sang tab k·∫ø ti·∫øp */
      pressNext: () => {
        const currentIndex = state.index;
        const nextRoute = state.routes[currentIndex + 1];
        if (nextRoute) {
          navigation.navigate(nextRoute.name);
          onTabChange?.(nextRoute.name);
        }
      },
      /** Quay l·∫°i tab tr∆∞·ªõc */
      pressPrevious: () => {
        const currentIndex = state.index;
        const prevRoute = state.routes[currentIndex - 1];
        if (prevRoute) {
          navigation.navigate(prevRoute.name);
          onTabChange?.(prevRoute.name);
        }
      },
    }));

    return (
      <View style={styles.tabBar}>
        {visibleRoutes.map((route: any, index: number) => {
          const isFocused = state.index >= index + 1; // +1 v√¨ slice(1)

          return (
            <View
              key={route.key}
              style={[styles.tabItem, isFocused && styles.tabItemActive]}
            />
          );
        })}

        <Text style={styles.tabItemText}>
          {t('STEP_NUMBER_OF_TOTAL', {
            number: Math.min(state.index, visibleRoutes.length),
            total: visibleRoutes.length,
          })}
        </Text>
      </View>
    );
  },
);

export default TabbarOnboarding;
