import { FlatList, Image, Text, View } from 'react-native';
import { styles } from './styles';
import { useTranslation } from 'react-i18next';
import { FieldArray, Formik, getIn } from 'formik';
import { ARR_KEYBOARD_NUMBER, initialValues, LIST_ITEMS } from './model';
import { KeyboardAwareScrollView } from 'react-native-keyboard-controller';
import { ScrollView } from 'react-native-gesture-handler';
import { useMemo, useRef } from 'react';
import { Button, Input } from 'tradewize-component';
import { globalStyles } from '../../global/styles';
import { FieldItem } from '../EnterYourInformation/model';
import colors from '../../theme/colors';
import { icon_minus } from '../../assets/images';
import { getWorkReferencesSchema } from '../../validators/register';
import { generateUUID } from '../../utils';
import {
  handleSubmitWorkReferences,
  tabbarRegisterRef,
} from '../../helper/register';

const ARR_MAX_LENGTH = [{ REFERENCE_PHONE: 12 }];

const getMaxLength = (fieldName: string) => {
  const item = ARR_MAX_LENGTH.find(obj => {
    return fieldName in obj;
  });
  return item ? item[fieldName as keyof typeof item] : undefined;
};

const WorkReferences = () => {
  const { t } = useTranslation();
  const pushFormRef = useRef<any>(null);
  const formRef = useRef<any>(null);

  // eslint-disable-next-line react-hooks/exhaustive-deps
  const validationSchema = useMemo(() => getWorkReferencesSchema(), [t]);

  return (
    <View style={globalStyles.flex}>
      <KeyboardAwareScrollView
        ScrollViewComponent={ScrollView}
        style={styles.container}
        bottomOffset={48}
      >
        <Text style={styles.referenceDescription}>
          {t('REFERENCE_DESCRIPTION')}{' '}
          <Text style={styles.minimumReference}>
            ({t('MINIMUM_1_REFERENCE')})
          </Text>
        </Text>

        <View style={styles.contentContainer}>
          <Formik
            innerRef={formRef}
            initialValues={initialValues}
            onSubmit={handleSubmitWorkReferences}
            validationSchema={validationSchema}
          >
            {({ values, errors, touched, setFieldTouched, setFieldValue }) => {
              // ‚úÖ G·∫Øn refIndex v√†o t·ª´ng field
              const arrFinal = values?.references?.flatMap((_, refIndex) =>
                LIST_ITEMS.map((item: FieldItem) => ({
                  ...item,
                  refIndex,
                })),
              );

              return (
                <FieldArray name="references">
                  {({ push }) => {
                    pushFormRef.current = push;

                    return (
                      <FlatList
                        contentContainerStyle={styles.contentListContainer}
                        scrollEnabled={false}
                        nestedScrollEnabled
                        data={arrFinal || []}
                        keyExtractor={(item, index) =>
                          `${item.refIndex}-${
                            'label' in item ? item?.label : ''
                          }-${index}`
                        }
                        renderItem={({ item }) => {
                          const refIndex = item.refIndex;
                          const fieldName =
                            'label' in item ? item?.label : 'COMPANY_NAME';
                          const fieldPath = `references[${refIndex}].${fieldName}`;
                          const error = getIn(errors, fieldPath);
                          const touch = getIn(touched, fieldPath);
                          const isFirstField =
                            'label' in item && item?.label === 'COMPANY_NAME';

                          return (
                            <View style={styles.referenceItemContainer}>
                              {isFirstField && (
                                <View style={styles.referenceItemHeader}>
                                  <Text style={styles.referenceItemLabel}>
                                    {t('REFERENCE_NUMBER', {
                                      number: refIndex + 1,
                                    }).toUpperCase()}
                                  </Text>

                                  {values.references.length > 1 && (
                                    <Button
                                      style={styles.buttonRemoveReference}
                                      variant="ghost"
                                      size="small"
                                      onPress={() => {
                                        const refs = [...values.references];
                                        refs.splice(refIndex, 1);
                                        setFieldValue('references', refs);
                                      }}
                                    >
                                      <Image
                                        source={icon_minus}
                                        style={styles.iconMinus}
                                      />
                                    </Button>
                                  )}
                                </View>
                              )}

                              <Text style={styles.labelField}>
                                {t('label' in item ? item?.label : '')}{' '}
                                {('isRequired' in item
                                  ? item?.isRequired
                                  : false) && (
                                  <Text style={styles.requiredField}>(*)</Text>
                                )}
                              </Text>

                              <Input
                                maxLength={getMaxLength(fieldName)}
                                placeholder={t(
                                  'placeholder' in item
                                    ? item?.placeholder
                                    : '',
                                )}
                                keyboardType={
                                  ARR_KEYBOARD_NUMBER.includes(
                                    fieldName as string,
                                  )
                                    ? 'number-pad'
                                    : 'default'
                                }
                                value={getIn(values, fieldPath)}
                                onChangeText={(text: string) => {
                                  if (ARR_KEYBOARD_NUMBER.includes(fieldName)) {
                                    const digitsOnly = text.replace(/\D/g, '');
                                    let formatted = digitsOnly;
                                    if (
                                      digitsOnly.length > 3 &&
                                      digitsOnly.length <= 6
                                    ) {
                                      formatted = `${digitsOnly.slice(
                                        0,
                                        3,
                                      )}-${digitsOnly.slice(3)}`;
                                    } else if (digitsOnly.length > 6) {
                                      formatted = `${digitsOnly.slice(
                                        0,
                                        3,
                                      )}-${digitsOnly.slice(
                                        3,
                                        6,
                                      )}-${digitsOnly.slice(6, 10)}`;
                                    }
                                    setFieldValue(
                                      fieldPath,
                                      fieldName === 'REFERENCE_PHONE'
                                        ? formatted
                                        : digitsOnly,
                                    );
                                  } else {
                                    setFieldValue(fieldPath, text);
                                  }
                                }}
                                style={styles.groupFieldItemInput}
                                inputContainerStyle={[
                                  styles.groupFieldItemInputContainer,
                                  'type' in item &&
                                    item?.type === 'textarea' &&
                                    styles.groupFieldItemInputContainerTextarea,
                                ]}
                                isError={!!(error && touch)}
                                onFocus={() =>
                                  setFieldTouched(fieldPath, false)
                                }
                                onBlur={() => setFieldTouched(fieldPath, true)}
                                borderColorActive={colors.mainColorPrimary}
                                borderColor={colors.grayScale40}
                                placeholderTextColor={colors.grayScale70}
                              />
                              {error && touch && (
                                <Text style={globalStyles.errorText}>
                                  {error}
                                </Text>
                              )}
                            </View>
                          );
                        }}
                      />
                    );
                  }}
                </FieldArray>
              );
            }}
          </Formik>
        </View>
      </KeyboardAwareScrollView>

      <Button
        style={styles.buttonAddReference}
        variant="ghost"
        size="small"
        title={t('ADD_ANOTHER_REFERENCE')}
        onPress={() =>
          pushFormRef.current({
            ID: generateUUID(),
            COMPANY_NAME: '',
            COMPANY_ADDRESS: '',
            REFERENCE_NAME: '',
            REFERENCE_PHONE: '',
            REFERENCE_EMAIL: '',
            RELATIONSHIP: '',
          })
        }
        textStyle={styles.buttonAddReferenceText}
      />

      <View style={styles.groundButtonContainer}>
        <Button
          variant="secondary"
          style={[styles.btnGround, styles.btnGroundBack]}
          title={t('BACK')}
          textStyle={styles.buttonBackText}
          onPress={() => tabbarRegisterRef.current?.pressPrevious()}
        />
        <Button
          variant="primary"
          style={[styles.btnGround, styles.btnGroundNext]}
          title={t('NEXT')}
          textStyle={styles.buttonNextText}
          onPress={() => formRef.current?.handleSubmit()}
        />
      </View>
    </View>
  );
};

export default WorkReferences;
