import {
  Image,
  Keyboard,
  Platform,
  TouchableNativeFeedback,
  View,
} from 'react-native';
import { Button, Text } from 'tradewize-component';
import { styles } from './styles';
import { KeyboardAvoidingView } from 'react-native-keyboard-controller';
import {
  globalStyles,
  keyboardBehavior,
  keyboardOffset,
} from '../../global/styles';
import {
  iconEmail,
  iconPhone,
  verifyEmail,
  verifyPhone,
} from '../../assets/images';
import { useTranslation } from 'react-i18next';
import { OtpInput, OtpInputRef } from 'react-native-otp-entry';
import { useEffect, useRef, useState } from 'react';
import colors from '../../theme/colors';
import { handleVerifyOtpAccount } from '../../helper/register';
import { useDispatch, useSelector } from 'react-redux';
import { RootState } from '../../redux/store/rootReducer';
import { useCountdown } from '../../hooks/useCountDown';
import {
  clearStateRegisterAsync,
  sendOTPVerifyAccountAsync,
} from '../../redux/features/register/actions';
import {
  hideAlertAsync,
  showAlertAsync,
} from '../../redux/features/app/actions';
import { NavigationService } from '../../services/NavigationService';
import { SCREENS_ENUM } from '../../types/global';

const VerifyAccount = () => {
  const { t } = useTranslation();
  const otpInputRef = useRef<OtpInputRef>(null);
  const dispatch = useDispatch();

  const { createInformation, verifyOtpAccount, sendOTPVerifyAccount } =
    useSelector((state: RootState) => state.register);

  const loadingOverlay = useSelector(
    (state: RootState) => state.app.loadingOverlay,
  );

  const [otp, setOtp] = useState<string>('');
  const [typeVerify, setTypeVerify] = useState<'email' | 'phone'>('phone');
  const [isResendCode, setIsResendCode] = useState(false);
  const [focusOtp, setFocusOtp] = useState(false);

  const { formatted, start, reset, stop } = useCountdown(59 * 1000, () => {
    setIsResendCode(true);
  });

  useEffect(() => {
    setTimeout(() => {
      start();
    }, 150);
  }, [start]);

  useEffect(() => {
    const state =
      sendOTPVerifyAccount?.[
        typeVerify === 'email' ? 'verifyEmail' : 'verifyPhone'
      ];

    if (state?.data && !state?.error && !loadingOverlay?.isVisible) {
      reset();
      setIsResendCode(false);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [sendOTPVerifyAccount, loadingOverlay?.isVisible, typeVerify]);

  useEffect(() => {
    const state =
      verifyOtpAccount?.[
        typeVerify === 'email' ? 'verifyEmail' : 'verifyPhone'
      ];

    if (!state?.error && state?.data && !loadingOverlay?.isVisible) {
      if (typeVerify === 'phone') {
        // X√°c th·ª±c phone th√†nh c√¥ng ‚Üí chuy·ªÉn sang email
        reset();
        setIsResendCode(false);
        otpInputRef?.current?.clear();
        setTypeVerify('email');
      }

      if (typeVerify === 'email') {
        // X√°c th·ª±c email th√†nh c√¥ng ‚Üí done
        stop();
        setIsResendCode(true);
        dispatch(
          showAlertAsync({
            isBackdropPress: false,
            visible: true,
            title: t('SUCCESS'),
            message: t('VERIFY_ACCOUNT_SUCCESS'),
            type: 'success',
            buttonText: t('CONFIRM'),
            messageStyle: {
              textAlign: 'left',
            },
            onClose: () => {
              dispatch(hideAlertAsync());
              dispatch(clearStateRegisterAsync());
              NavigationService.replace(
                SCREENS_ENUM.AccountActivationStatusScreen,
                {
                  accountId: createInformation?.data?.accountId,
                },
              );
            },
          }),
        );
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [
    typeVerify,
    verifyOtpAccount.verifyPhone,
    verifyOtpAccount.verifyEmail,
    loadingOverlay?.isVisible,
    verifyOtpAccount,
  ]);

  useEffect(() => {
    if (
      (verifyOtpAccount?.verifyPhone?.error ||
        verifyOtpAccount?.verifyEmail?.error) &&
      !loadingOverlay?.isVisible
    ) {
      otpInputRef?.current?.clear();
    }
  }, [
    loadingOverlay?.isVisible,
    typeVerify,
    verifyOtpAccount?.verifyEmail?.error,
    verifyOtpAccount?.verifyPhone?.error,
  ]);

  return (
    <KeyboardAvoidingView
      behavior={keyboardBehavior}
      keyboardVerticalOffset={keyboardOffset}
      style={styles.container}
    >
      <TouchableNativeFeedback onPress={() => Keyboard.dismiss()}>
        <View style={styles.containerContent}>
          <Image
            source={typeVerify === 'email' ? verifyEmail : verifyPhone}
            style={styles.verifyImage}
          />
          <Text style={styles.title}>
            {t('VERIFY_ACCOUNT_TITLE', {
              type:
                typeVerify === 'email'
                  ? t('EMAIL_ADDRESS').toLocaleLowerCase()
                  : t('PHONE_NUMBER').toLocaleLowerCase(),
            })}
          </Text>

          <View style={styles.boxViewText}>
            <Image
              resizeMode="contain"
              source={typeVerify === 'email' ? iconEmail : iconPhone}
              style={styles.iconPhone}
            />
            <Text style={styles.boxViewTextTitle}>
              {typeVerify === 'email'
                ? createInformation?.data?.values?.EMAIL
                : createInformation?.data?.values?.PHONE_NO}
            </Text>
          </View>

          <Text style={styles.textDescriptionVerifyAccount}>
            {t('VERIFY_ACCOUNT_DESC', {
              type:
                typeVerify === 'email'
                  ? t('EMAIL').toLocaleLowerCase()
                  : t('PHONE_NUMBER').toLocaleLowerCase(),
            })}
          </Text>

          <View style={styles.containerOtp}>
            <OtpInput
              ref={otpInputRef}
              numberOfDigits={6}
              onTextChange={(code: string) => {
                if (code?.length >= 6) {
                  otpInputRef?.current?.blur();

                  setTimeout(() => {
                    handleVerifyOtpAccount({ type: typeVerify, otp: code });
                  }, 150);
                }
                setOtp(code);
              }}
              onFocus={() => {
                setFocusOtp(true);
              }}
              onBlur={() => {
                setFocusOtp(false);
              }}
              theme={{
                pinCodeTextStyle: {
                  fontSize: 16,
                  fontWeight: '400',
                  color: colors.black,
                },
                pinCodeContainerStyle: {
                  ...styles.pinCodeContainer,
                  borderColor:
                    verifyOtpAccount?.[
                      typeVerify === 'email' ? 'verifyEmail' : 'verifyPhone'
                    ]?.error &&
                    !focusOtp &&
                    !loadingOverlay?.isVisible
                      ? colors.alertColorError
                      : colors.borderGray,
                },
              }}
              focusStickBlinkingDuration={500}
              type="numeric"
              textProps={{
                accessibilityRole: 'text',
                accessibilityLabel: 'OTP digit',
                allowFontScaling: false,
              }}
              textInputProps={{
                keyboardType: 'numeric',
                autoComplete:
                  Platform.OS === 'android' ? 'sms-otp' : 'one-time-code',
                textContentType: 'oneTimeCode',
              }}
              focusColor={colors.mainColorPrimary}
              autoFocus={false}
            />
            {verifyOtpAccount?.[
              typeVerify === 'email' ? 'verifyEmail' : 'verifyPhone'
            ]?.error &&
              !focusOtp &&
              !loadingOverlay?.isVisible && (
                <Text style={[globalStyles.errorText, styles.errorTextOtp]}>
                  {
                    verifyOtpAccount?.[
                      typeVerify === 'email' ? 'verifyEmail' : 'verifyPhone'
                    ]?.error
                  }
                </Text>
              )}
            <Button
              onPress={() => {
                handleVerifyOtpAccount({ type: typeVerify, otp });
              }}
              fullWidth
              style={styles.buttonVerify}
              title={t('VERIFY')}
            />
          </View>

          <View style={styles.containerButtonResendCode}>
            <Text style={styles.textDidNotReceiveCode}>
              {t('DID_NOT_RECEIVE_CODE')}
            </Text>
            <Button
              onPress={() => {
                if (isResendCode) {
                  dispatch(
                    sendOTPVerifyAccountAsync({
                      receiveType: typeVerify === 'email' ? 0 : 1,
                      email: createInformation?.data?.values?.EMAIL ?? '',
                    }),
                  );
                }
              }}
              variant="ghost"
              style={styles.buttonResendCode}
              title={
                t('RESEND_CODE', { type: t('CODE').toLocaleLowerCase() }) +
                (!isResendCode ? ` (${formatted})` : '')
              }
              textStyle={styles.textResendCode}
            />
          </View>
        </View>
      </TouchableNativeFeedback>
    </KeyboardAvoidingView>
  );
};

export default VerifyAccount;
